import { Component, OnInit, ViewChild } from '@angular/core';
import { FbConstants } from '../../../../../../src/app/utils/fb-constants';
import { FbApiConstants } from '../../../../../../src/app/utils/fb-api-constants';
import { Apps } from '../../../../../../src/app/models/apps';
import { AppsService } from '../../../../../../src/app/commonServices/apps.service';
import Swal, { SweetAlertIcon } from 'sweetalert2';
import { LoaderService } from '../../../../../../src/app/commonServices/loader.service';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { AlertUtil } from 'src/app/utils/alert-util';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})

export class DashboardComponent implements OnInit {
  loggedInUser;
  appList: Apps[] = [];
  items: Apps[] = [];
  newApps = [];
  myApps = [];
  sharedApps = [];
  pinnedApps = [];
  unpinnedApps = [];
  selectedIndex;
  build_id: any;

  constructor( private activatedRoute : ActivatedRoute,private fbConstants: FbConstants, public fbApiConstants: FbApiConstants,
               private appsService: AppsService, public loaderService: LoaderService ) { }
  ngOnInit() {
    this.loggedInUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
    this.loadAllApps();
    if (localStorage.getItem('active') === undefined || localStorage.getItem('active') === null ) {
      this.selectedIndex = 0;
    } else {
      this.selectedIndex = JSON.parse(localStorage.getItem('active'));
    }

    this.activatedRoute.queryParams.subscribe(params => {
      this.build_id = params['build_id'];
      
  });

  }
  loadAllApps() { // loads all installed and uploaded apps separately in the view
    this.loaderService.showLoader = true;
    this.appsService.getAllApps().subscribe(result => {
      this.appList = result;
      this.loaderService.showLoader = false;

      this.myApps = this.appList.filter(appObj => appObj.share === 'false');
      this.myApps = this.myApps.filter(appObj => appObj.version.bundle.pin === true)
        .concat(this.myApps.filter(appObj => appObj.version.bundle.pin === false));

      this.sharedApps = this.appList.filter(appObj => appObj.share === 'true');
      this.sharedApps = this.sharedApps.filter(appObj => appObj.version.bundle.pin === true)
        .concat(this.sharedApps.filter(appObj => appObj.version.bundle.pin === false));

      this.pinnedApps = this.appList.filter(appObj => appObj.version.bundle.pin === true);
      this.unpinnedApps = this.appList.filter(appObj => appObj.version.bundle.pin === false);
      this.newApps = this.appList;
   } ,
   (err) => {
     AlertUtil.showAlert(this.fbConstants.dialog_titles.oops, err ||
       this.fbConstants.dialog_messages.commonServerError, this.fbConstants.dialog_type.error as SweetAlertIcon)
    }
  );
}
onTabChanged(tabChangeEvent: MatTabChangeEvent) {
  localStorage.setItem('active', JSON.stringify(tabChangeEvent.index));
  if (tabChangeEvent.index === 1) {
    this.newApps = this.sharedApps;
  } else {
    this.newApps = this.myApps;
  }
}
}
