import { Component, OnInit, Inject, NgZone } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { FbConstants } from 'src/app/utils/fb-constants';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { LoginService } from 'src/app/commonServices/login.service';
import { NetworkService } from 'src/app/commonServices/network.service';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { CurrentUser } from 'src/app/models/current-user';
import { Observable } from 'rxjs';
import { User } from 'src/app/models/user';
import Swal from 'sweetalert2';
import { FieldErrorStateMatcher } from 'src/app/models/errorStateMatcher';
import { ToolbarService } from 'src/app/commonServices/toolbar.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css'],
  preserveWhitespaces: false
})
export class SigninComponent implements OnInit {

  matcher = new FieldErrorStateMatcher();
  formData = new FormData();
  userInfo: CurrentUser;
  api$: Observable<string>;
  isEmptyField: boolean;
  isDirtyField: boolean;
  isBothEmpty: boolean;
  isPasswordEmpty: boolean;
  isRememberMe : boolean = false;

  build_id: any;

  constructor( private activatedRoute :ActivatedRoute,
                private router: Router,
               private authService: FirebaseAuthService,
               public fbConstants: FbConstants,
               public dialog: MatDialog,
               private dialogRef: MatDialogRef<SigninComponent>,
               public loginService: LoginService,
               @Inject(MAT_DIALOG_DATA) public data: any,
               private loaderService: LoaderService,
               private zone: NgZone,
               private networkService: NetworkService,
               private toolbarService: ToolbarService,
               private snackBar: MatSnackBar
             ) { }

  loginForm = new UntypedFormGroup({
    email: new UntypedFormControl('', Validators.compose([
                               Validators.required,
                               Validators.pattern(this.fbConstants.patterns.email)])),
    password : new UntypedFormControl('', Validators.compose([
                                   Validators.required,
                                   Validators.pattern(
                                      this.fbConstants.patterns.string_without_space)
                              ]))});
  ngOnInit() {
     this.loginService.isNewUser = false;
     this.activatedRoute.queryParams.subscribe(params => {
        this.build_id = params['build_id'];
    });
  }
  onClickSignup() {
    this.loginService.isNewUser = true;
    }
  onClickForgotPassword(email: string) {
    this.isEmptyField = false;
    this.isDirtyField = false;
    if (email && !this.loginForm.get('email').hasError('pattern')) {
      this.authService.resetPassword(email).then(res => {
        this.dialogRef.close();
        this.showResetPasswordEmailAlert();
      }).catch(error => {
        if (error.code === this.fbConstants.firebaseErrorCodes.invalidEmail) {
          // Swal.fire('Invalid email!', this.fbConstants.dialog_messages.invalidEmail,
          // 'error');
          this.snackBar.open(this.fbConstants.dialog_messages.invalidEmail, '', {
            duration: 3000,
            panelClass: this.fbConstants.snackbarType.error
          });
        } else if (error.code === this.fbConstants.firebaseErrorCodes.userNotFound) {
          this.showResetPasswordEmailAlert();
        } else {
          Swal.fire('Oops!', error.message, 'error');
        }
      });
    } else if (email === '') {
      this.isEmptyField = true;
    } else if (this.loginForm.get('email').hasError('pattern')) {
      this.isDirtyField = true;
    } else {
      // this.isEmptyField = true;
    }
  }

  private showResetPasswordEmailAlert() {
    Swal.fire({
      title: this.fbConstants.dialog_titles.reset_password,
      html:  this.fbConstants.dialog_messages.password_reset +
      this.fbConstants.dialog_messages.email_note ,
      icon: 'success',
      confirmButtonText: 'OK',
    });
  }

  googleLogin() {
    // localStorage.clear();
    this.formData.append(this.fbConstants.user_details.login_type,
                         this.fbConstants.login_types.google);
    this.authService.doGoogleLogin().then(result => {
      this.dialogRef.close();
      this.loaderService.showLoader = true;
      let user: User = {} as User;
      user = JSON.parse(JSON.stringify(result.additionalUserInfo.profile));
      user.loginType = this.fbConstants.login_types.google;
      this.networkService.getIcon(user.picture).subscribe((data: any) => {
        let fileName = (user.picture).split('/').pop();
        if (!fileName.endsWith('.jpg')) {
          fileName = fileName + '.jpg';
        }
        const file  = new File([data], fileName );
        this.formData.append(this.fbConstants.user_details.profileImage, file, file.name);
        this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
          .user.stsTokenManager.accessToken, this.formData)
          .subscribe( res => {
          if (res.data.json_token.access) {
            this.userInfo = res;
            const userData = JSON.stringify(this.userInfo);
            localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
            localStorage.setItem(this.fbConstants.localStorageItems.plan_type,
              JSON.stringify(this.userInfo.plan));
            localStorage.setItem(this.fbConstants.localStorageItems.login_type,
              this.fbConstants.login_types.google);
            localStorage.setItem(this.fbConstants.localStorageItems.rememberMyLogin, JSON.stringify(this.isRememberMe));
            this.onSuccess();
            }
          },
          (err) => {
            this.loaderService.showLoader = false;
            this.zone.run(() => {
              Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError,
              'error');
            });
            }
          );
             });
        }, (err) => {
          this.loaderService.showLoader = false;
          if (err.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
            this.zone.run(() => {
              // Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError,
              // 'warning');
              this.snackBar.open(this.fbConstants.dialog_messages.duplicateAccountError, '', {
                duration: 3000,
                panelClass: this.fbConstants.snackbarType.error
              });
            });
          } else if ((err.code === this.fbConstants.firebaseErrorCodes.popupClose) ||
              (err.code === this.fbConstants.firebaseErrorCodes.conflictingPopups)) {
          } else {
            this.zone.run(() => {
              Swal.fire('Oops!', err.message ||
              this.fbConstants.dialog_messages.commonServerError, 'error');
            });
          }
        });
}
facebookLogin() {
  // localStorage.clear();
  this.formData.append(this.fbConstants.user_details.login_type,
                       this.fbConstants.login_types.facebook);
  this.authService.doFacebookLogin().then(result => {
    if (result.user.emailVerified === false && result.user.email === null) {
      Swal.fire('Oops!', this.fbConstants.dialog_messages.facebook_note, 'error');
    } else {
       this.dialogRef.close();
       let user: User = {} as User;
       this.loaderService.showLoader = true;
       user = result.additionalUserInfo.profile;
       user.photoUrl = result.additionalUserInfo.profile.picture.data.url;
       user.loginType = this.fbConstants.login_types.facebook;
       this.networkService.getIcon(user.photoUrl).subscribe((data: any) => {
        let fileName = (user.photoUrl).split('/').pop();
        if (!fileName.endsWith('.jpg')) {
          fileName = fileName + '.jpg';
        }
        const file  = new File([data], fileName );
        this.formData.append(this.fbConstants.user_details.profileImage, file, file.name);
        this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
          .user.stsTokenManager.accessToken, this.formData)
          .subscribe( res => {
            if (res.data.json_token.access) {
              this.userInfo = res;
              const userData = JSON.stringify(this.userInfo);
              localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
              localStorage.setItem(this.fbConstants.localStorageItems.plan_type,
                JSON.stringify(this.userInfo.plan));
              localStorage.setItem(this.fbConstants.localStorageItems.login_type,
                this.fbConstants.login_types.facebook);
              localStorage.setItem(this.fbConstants.localStorageItems.rememberMyLogin, JSON.stringify(this.isRememberMe));
              this.onSuccess();
            }
          },
          (err) => {
            this.loaderService.showLoader = false;
            this.zone.run(() => {
              Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError,
              'error');
            });
            });
          });
        }
      }, (err) => {
        this.loaderService.showLoader = false;
        if (err.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
          this.zone.run(() => {
            // Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError,
            // 'warning');
            this.snackBar.open(this.fbConstants.dialog_messages.duplicateAccountError, '', {
              duration: 3000,
              panelClass: this.fbConstants.snackbarType.error
            });
          });
        } else if ((err.code === this.fbConstants.firebaseErrorCodes.popupClose) ||
          (err.code === this.fbConstants.firebaseErrorCodes.conflictingPopups)) {
        } else {
          this.zone.run(() => {
            Swal.fire('Oops!', err.message ||
            this.fbConstants.dialog_messages.commonServerError, 'error');
          });
        }
      });
}
  public onEmailValueChange(newEmail: string) {
    if (newEmail != null) {
      this.loginForm.patchValue({
        email: newEmail,
      });
    }
  }
  private onSuccess() {
      this.zone.run(() => {
        sessionStorage.setItem(this.fbConstants.sessionStorageItems.isNavigatedFromLogin, JSON.stringify(true));
        // this.router.navigateByUrl(this.fbConstants.mob_router_links.dashboard_component,{ queryParams: { 'build_id': this.build_id }});
        this.router.navigate([this.fbConstants.mob_router_links.dashboard_component],
          {queryParams: {build_id: this.build_id}});
          
      });
      this.loaderService.showLoader = false;
  }
  public rememberCheck(value) {
    this.isRememberMe = value
  }
onClickLogin(username: string, password: string) {
  // localStorage.clear();
  if (username === '' && password === '') {
    this.isEmptyField = true;
    this.isPasswordEmpty = true;
  } else if (username === '') {
    this.isEmptyField = true;
  } else if (password === '') {
    this.isPasswordEmpty = true;
  } else {
    if (this.loginForm.valid) {
      this.authService.loginWithEmail(username, password).then((result) => {
      if (result.user.emailVerified === true) {
        const user: User = {} as User;
        user.email = this.loginForm.value.email;
        this.loaderService.showLoader = true;
        this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
          .user.stsTokenManager.accessToken, this.formData)
          .subscribe(res => {
            if (res.data.json_token.access) {
              this.userInfo = res;
              const userData = JSON.stringify(this.userInfo);
              localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
              localStorage.setItem(this.fbConstants.localStorageItems.plan_type,
                JSON.stringify(this.userInfo.plan));
              localStorage.setItem(this.fbConstants.localStorageItems.login_type,
                this.fbConstants.login_types.other);
              localStorage.setItem(this.fbConstants.localStorageItems.rememberMyLogin, JSON.stringify(this.isRememberMe));
              this.dialogRef.close();
              setTimeout(() => {
                this.onSuccess();
              }, 1000);
            }
          },
         (err) => {
          this.loaderService.showLoader = false;
          this.zone.run(() => {
            Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError,
            'error');
          });
        });
      } else {
        this.zone.run(() => {
          // Swal.fire('Email not verified',
          // this.fbConstants.dialog_messages.emailNotVerified, 'error');
          this.snackBar.open(this.fbConstants.dialog_messages.emailNotVerified, '', {
            duration: 3000,
            panelClass: this.fbConstants.snackbarType.error
          });
          this.authService.sendEmail();
          });
      }
    }).catch(error => {
      this.loaderService.showLoader = false;
      if (error.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
        this.zone.run(() => {
          // Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError,
          // 'error');
          this.snackBar.open(this.fbConstants.dialog_messages.duplicateAccountError, '', {
            duration: 3000,
            panelClass: this.fbConstants.snackbarType.error });
        });
      } else if (error.code === this.fbConstants.firebaseErrorCodes.userNotFound) {
        this.zone.run(() => {
          this.loginForm.get('password').reset();
          this.snackBar.open(this.fbConstants.dialog_messages.invalidPassword, '', {
            duration: 3000,
            panelClass: this.fbConstants.snackbarType.error
          });
        });
      } else if (error.code === this.fbConstants.firebaseErrorCodes.passwordError) {
          this.zone.run(() => {
            // Swal.fire('Login failed!', this.fbConstants.dialog_messages.invalidPassword,
            // 'error');
            this.snackBar.open(this.fbConstants.dialog_messages.invalidPassword, '', {
              duration: 3000,
              panelClass: this.fbConstants.snackbarType.error
            });
            this.loginForm.get('password').reset();
          });
      } else if (error.code === this.fbConstants.firebaseErrorCodes.invalidEmail) {
        this.zone.run(() => {
          // Swal.fire('Login failed!', this.fbConstants.dialog_messages.invalidPassword,
          // 'error');
          this.snackBar.open(this.fbConstants.dialog_messages.invalidPassword, '', {
            duration: 3000,
            panelClass: this.fbConstants.snackbarType.error
          });
          this.loginForm.get('password').reset();
        });
      } else {
        this.zone.run(() => {
          Swal.fire('Oops!', error.message, 'error');
        });
      }
      });
    } // if valid
  } // else
 }
  close() {
    this.dialogRef.close();
  }

  onClickPrivacyPolicy(){
    window.open( environment.urls.domain_url + '/privacy-policy', '_blank');
  }
}
