import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CurrentUser } from 'src/app/models/current-user';
import { Device } from 'src/app/models/device';
import { FbConstants } from 'src/app/utils/fb-constants';

@Component({
  selector: 'app-my-devices',
  templateUrl: './my-devices.component.html',
  styleUrls: ['./my-devices.component.css']
})
export class MyDevicesComponent implements OnInit {

  public devices:Device[] = [];

  constructor(private router: Router,
    private fbConstants: FbConstants,
  ) { }

  ngOnInit() {
    const currentUser: CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));    
    this.devices = currentUser.data.udid_list;
  }

  onClickBack() {
    this.router.navigate([this.fbConstants.mob_router_links.dashboard_component])
  }

}
