import { Injectable } from '@angular/core';
import { FbConstants } from '../utils/fb-constants';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FbApiConstants } from '../utils/fb-api-constants';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { LoaderService } from './loader.service';
import { environment } from '../../environments/environment';
import { CurrentUser } from '../models/current-user';


@Injectable({   providedIn: 'root' })
export class NetworkService {
  httpHeader;
  constructor( private fbApiConstants: FbApiConstants,
               private fbConstants: FbConstants,
               private http: HttpClient,
               private loaderService: LoaderService) { }

  private setRequestHeader() {
    let userInfo = localStorage.getItem(this.fbConstants.localStorageItems.userInfo);
    if (userInfo !== null) {
      let userInfoParsed: CurrentUser = JSON.parse(userInfo);
      if (userInfoParsed != null && userInfoParsed.data.json_token != null) {
        this.httpHeader = new HttpHeaders().set(this.fbConstants.auth_params.authorization,
          this.fbConstants.auth_params.token_prefix +
          userInfoParsed.data.json_token.access);
        return;
      }
    }
    this.httpHeader = '';
  }
  postRequest(params, url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.post(environment.urls.baseUrl + url, params,
           {headers: this.httpHeader, reportProgress: true})
           .pipe(map(this.extractData));
  }
  postRequestJson(body, url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.post(environment.urls.baseUrl + url, body,
           {headers: this.httpHeader, reportProgress: true})
           .pipe(map(this.extractData));
  }
  putRequest(params, url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();

    return this.http.put(environment.urls.baseUrl + url, params,
           {headers: this.httpHeader})
           .pipe(map(this.extractData));
  }
  getRequest(url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.get( environment.urls.baseUrl + url, {headers: this.httpHeader})
    .pipe(map(this.extractData));
  }
  getRequestMock(url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.get( url, {headers: this.httpHeader})
    .pipe(map(this.extractData));
  }
  deleteRequest(url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    // return this.http.delete(environment.urls.baseUrl + url + '/' + id , {headers: this.httpHeader})
    //        .pipe(map(this.extractData));
    return this.http.delete(environment.urls.baseUrl + url, {headers: this.httpHeader})
           .pipe(map(this.extractData));
  }

  deleteRequestParams(params, url, showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    const options = {
      headers:this.httpHeader,
      body: {
        'organization': params,
      }
    }
    return this.http.delete(environment.urls.baseUrl + url, options)
            .pipe(map(this.extractData));
  }

  deleteRequestWithId(url, id,showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.delete(environment.urls.baseUrl + url + '/' + id +
           '/', {headers: this.httpHeader} )
           .pipe(map(this.extractData));
  }
  deleteRequestWithOrgId(url, id,organization,showLoader?: boolean): Observable<any> {
    this.setRequestHeader();
    return this.http.delete(environment.urls.baseUrl + url + '/' + id +'/' + organization +
           '/', {headers: this.httpHeader} )
           .pipe(map(this.extractData));
  }

private extractData(res: Response) {
  if (res.status < 200 || res.status >= 300) {
        throw new Error('Bad response status: ' + res.status);
      }
  const body = res;
  return body || { };
}
  public getIcon(icon) {
      return this.http.get(icon, { responseType: 'blob'});
  }

  static isMobile(ua: string) {
    const mobileUserAgentPattern = /Mobile|iP(hone|od|ad)|Android|BlackBerry|IEMobile/;
    return mobileUserAgentPattern.test(ua);
  }
}
