import { Component, OnInit, NgZone, Inject, HostListener } from '@angular/core';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { FbConstants } from 'src/app/utils/fb-constants';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { UploadService } from '../../../../../../src/app/commonServices/upload.service';
import Swal from 'sweetalert2';
import { trimValidator } from 'src/app/utils/trim.validator';


@Component({
  selector: 'app-try-it-now',
  templateUrl: './try-it-now.component.html',
  styleUrls: ['./try-it-now.component.css']
})
export class TryItNowComponent implements OnInit {
  emailForm = new UntypedFormGroup({
    email: new UntypedFormControl('', Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.email), trimValidator
    ])),
    platform: new UntypedFormControl('2', [Validators.required, trimValidator])});
    showError: boolean;
    errorMessage;

  constructor(private dialogRef: MatDialogRef<TryItNowComponent>, private dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any, public fbConstants: FbConstants, private uploadService: UploadService,
    private zone: NgZone, private loaderService: LoaderService) { }

    // @HostListener('window:keyup.enter') onKeyUp() {
    //   this.dialogRef.disableClose = true;
    // }
  ngOnInit() {
  }
  onClickShare() {
  if (this.emailForm.invalid) {
    this.fbConstants.validation_messages.email.forEach(element => {
      if (this.emailForm.get('email').hasError(element.type)) {
        this.showError = true;
        this.errorMessage = element.message;
      }
    });
  } else {
    const formData = new FormData();
    formData.append('email', this.emailForm.value.email);
    formData.append('platform', this.emailForm.value.platform);
    this.uploadService.shareDemoBuild(formData).subscribe(result => {
      this.zone.run(() => {
        Swal.fire('Shared demo build', this.fbConstants.dialog_messages.email_success, 'success');
        this.dialogRef.close();
      }, (err) => {
          this.dialogRef.close();
          this.zone.run(() => {
            Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
          });
        });
      });
  }
}
}
