import { Component, OnInit, NgZone, Injector, Inject } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FbConstants } from 'src/app/utils/fb-constants';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import firebase from 'firebase/compat/app';
import 'firebase/auth';
import { User } from 'src/app/models/user';
import { MatDialog, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { LoginService } from 'src/app/commonServices/login.service';
import { CurrentUser } from 'src/app/models/current-user';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { NetworkService } from 'src/app/commonServices/network.service';
import Swal from 'sweetalert2';
import { LoginCheckService } from '../../mobileAppServices/login-check.service';

@Component({
  selector: 'app-download-page-login',
  templateUrl: './download-page-login.component.html',
  styleUrls: ['./download-page-login.component.css',
              '../../../assets/login_page/vendor/bootstrap/css/bootstrap.min.css',
              '../../../assets/login_page/fonts/font-awesome-4.7.0/css/font-awesome.min.css',
              '../../../assets/login_page/vendor/animate/animate.css',
              '../../../assets/login_page/vendor/css-hamburgers/hamburgers.min.css',
              '../../../assets/login_page/vendor/select2/select2.min.css',
              '../../../assets/login_page/css/util.css',
              '../../../assets/login_page/css/main.css']
})
export class DownloadPageLoginComponent implements OnInit {
  googleProvider = new firebase.auth.GoogleAuthProvider();
  facebookProvider = new firebase.auth.FacebookAuthProvider();
  isLoggedIn = false;
  user: User;
  show: boolean;
  formData = new FormData();
  userInfo: CurrentUser;
  build_id;
  component = 'download_login';
  isEmptyField: boolean;
  isDirtyField: boolean;
 // isBothEmpty: boolean;
  isEmailEmpty: boolean;
  isPasswordEmpty: boolean;

  constructor(public fbConstants: FbConstants, private activatedRoute: ActivatedRoute, private ngZone: NgZone,
              private router: Router, private networkService: NetworkService, private authService: FirebaseAuthService,
              private loaderService: LoaderService, public loginService: LoginService, private dialog: MatDialog,
              private injector: Injector, @Inject(MAT_DIALOG_DATA) public data: any,
              private dialogRef: MatDialogRef<DownloadPageLoginComponent>,
              private loginCheckService: LoginCheckService) {}

  loginToDownloadForm = new UntypedFormGroup({
    email: new UntypedFormControl('', Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.email)
    ])),
    password : new UntypedFormControl('', Validators.compose([
      Validators.required,
      // Validators.pattern(this.fbConstants.patterns.password),getAppList()
      Validators.pattern(this.fbConstants.patterns.string_without_space)
    ]))});

  ngOnInit() {
     this.loginService.isNewUser = false;
    //  this.activatedRoute.queryParams.subscribe(params => {
    //     this.build_id = params['build_id'];
    // });
    if (this.data) {
      this.build_id = this.data.build_id;
    }
  }

// email login
  onClickLogin(email: string, password: string) {
  //  this.isBothEmpty = false;
    this.isEmptyField = false;
    if (email === '' && password === '') {
      this.isEmailEmpty = true;
      this.isPasswordEmpty = true;
    } else if (email === '') {
      this.isEmailEmpty = true;
    } else if ( password === '') {
      this.isPasswordEmpty = true;
    } else {
      if ( this.loginToDownloadForm.valid ) {
        this.authService.loginWithEmail(email, password).then(result => {
          if (result.user.emailVerified === true) {
          const user: User = <User> {};
          user.email = this.loginToDownloadForm.value.email;
          this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
            .user.stsTokenManager.accessToken, this.formData).subscribe( res => {
              if (res.data.json_token.access) {
                  this.isLoggedIn = true;
                  this.userInfo = res;
                  const userData = JSON.stringify(this.userInfo);
                  localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
                  this.onSuccess();
              }
             },
             (err) => {
               // this.loaderService.show = false;
               this.ngZone.run(() => {
                Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
               });
              }
            );
          } else {
            this.ngZone.run(() => {
              Swal.fire('Email not verified', this.fbConstants.dialog_messages.emailNotVerified, 'error');
              });
          }
        }).catch(error => {
          // this.dialog.open(DialogComponent,
          //   { data: {message: error.message}, minWidth: '200px', maxWidth: '600px'});
          if (error.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
            // this.authService.handleMultipleProviderError(err);
            this.ngZone.run(() => {
              Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError, 'warning');
              });
          } else if (error.code === this.fbConstants.firebaseErrorCodes.userNotFound) {
            this.ngZone.run(() => {
            Swal.fire('User account does not exist!', this.fbConstants.dialog_messages.userNotFoundError, 'error');
              });
          } else if (error.code === this.fbConstants.firebaseErrorCodes.passwordError) {
              this.ngZone.run(() => {
                Swal.fire('Login failed!', this.fbConstants.dialog_messages.invalidPassword, 'error');
                });
          } else {
            this.ngZone.run(() => {
              Swal.fire('Oops!', error.message, 'error');
              });
          }
        });
      } // if valid
    } // else
  }

  // google login: login using popup
  onClickGoogleLogin() {
    this.show = true;
    this.formData.append(this.fbConstants.user_details.login_type, this.fbConstants.login_types.google);
    this.authService.doGoogleLogin().then(result => {
        let user: User = <User> {};
        user = JSON.parse(JSON.stringify(result.additionalUserInfo.profile));
        user.loginType = this.fbConstants.login_types.google;
        this.networkService.getIcon(user.picture).subscribe((data: any) => {
          const fileName = (user.picture).split('/').pop();
          const file  = new File([data], fileName );
          this.formData.append(this.fbConstants.user_details.profileImage, file, file.name);
          this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
          .user.stsTokenManager.accessToken, this.formData)
          .subscribe( res => {
            if (res.data.json_token.access) {
              this.isLoggedIn = true;
              this.userInfo = res;
              const userData = JSON.stringify(this.userInfo);
              localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
              localStorage.setItem(this.fbConstants.localStorageItems.plan_type, JSON.stringify(this.userInfo.plan));
              localStorage.setItem(this.fbConstants.localStorageItems.login_type, this.fbConstants.login_types.google);
              this.onSuccess();
            }
          },
          (err) => {
            this.show = false;
            this.ngZone.run(() => {
              Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
            });
            }
          );
                });
        }, (err) => {
          this.show = false;
          if (err.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
            // this.authService.handleMultipleProviderError(err);
            this.ngZone.run(() => {
              Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError, 'warning');

              });
            } else if ((err.code === this.fbConstants.firebaseErrorCodes.popupClose) ||
                       (err.code === this.fbConstants.firebaseErrorCodes.conflictingPopups)) {
            } else {
              this.ngZone.run(() => {
                Swal.fire('Oops!', err.message || this.fbConstants.dialog_messages.commonServerError, 'error');
                });
            }
          }
      );
  }

// facebook login : login using popup
  onClickFacebookLogin() {
    this.show = true;
    this.formData.append(this.fbConstants.user_details.login_type, this.fbConstants.login_types.google);
    this.authService.doFacebookLogin().then(result => {
       let user: User = <User> {};
       user = result.additionalUserInfo.profile;
       user.photoUrl = result.additionalUserInfo.profile.picture.data.url;
       user.loginType = this.fbConstants.login_types.facebook;
       this.networkService.getIcon(user.photoUrl).subscribe((data: any) => {
        const fileName = (user.photoUrl).split('/').pop();
        const file  = new File([data], fileName );
        this.formData.append(this.fbConstants.user_details.profileImage, file, file.name);
              this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
                .user.stsTokenManager.accessToken, this.formData)
                .subscribe( res => {
          if (res.data.json_token.access) {
            this.isLoggedIn = true;
            this.userInfo = res;
            const userData = JSON.stringify(this.userInfo);
            localStorage.setItem(this.fbConstants.localStorageItems.userInfo, userData);
            localStorage.setItem(this.fbConstants.localStorageItems.plan_type, JSON.stringify(this.userInfo.plan));
            localStorage.setItem(this.fbConstants.localStorageItems.login_type, this.fbConstants.login_types.facebook);
            this.onSuccess();
          }
         },
         (err) => {
          this.show = false;
           this.ngZone.run(() => {
            Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
           });
          }
        );
               });
      }, (err) => {
        this.show = false;
        if (err.code === this.fbConstants.firebaseErrorCodes.multipleprovider) {
          // this.authService.handleMultipleProviderError(err);
          this.ngZone.run(() => {
            Swal.fire('Oops!', this.fbConstants.dialog_messages.duplicateAccountError, 'warning');

            });
          } else if ((err.code === this.fbConstants.firebaseErrorCodes.popupClose) ||
                     (err.code === this.fbConstants.firebaseErrorCodes.conflictingPopups)) {
          } else {
            this.ngZone.run(() => {
              Swal.fire('Oops!', err.message || this.fbConstants.dialog_messages.commonServerError, 'error');
             });
          }
        }
    );
   this.show = true;
  }

  // new user sign up
  onClickSignUp() {
    this.loginService.isNewUser = true;
    // this.onSuccess();
  }

  // forgot password
  onClickForgotPassword(email: string) {
    this.isEmptyField = false;
    this.isDirtyField = false;
    if (email && !this.loginToDownloadForm.get('email').hasError('pattern')) {
      this.authService.resetPassword(email).then(res => {
        this.showResetPasswordEmailAlert();
      }).catch(error => {
        if (error.code === this.fbConstants.firebaseErrorCodes.invalidEmail) {
          Swal.fire('Invalid email!', this.fbConstants.dialog_messages.invalidEmail, 'error');
        } else if (error.code === this.fbConstants.firebaseErrorCodes.userNotFound) {
          this.ngZone.run(() => {
            this.showResetPasswordEmailAlert();
          });
        } else {
          Swal.fire('Oops!', error.message, 'error');
        }
      });
    } else if (this.loginToDownloadForm.get('email').hasError('pattern')) {
      this.isDirtyField = true;
    } else {
      this.isEmptyField = true;
    }
  }

  private showResetPasswordEmailAlert() {
    Swal.fire({
      title: this.fbConstants.dialog_titles.reset_password,
      html:  this.fbConstants.dialog_messages.password_reset +
      this.fbConstants.dialog_messages.email_note ,
      icon: 'success',
      confirmButtonText: 'OK',
    });
  }

  // after user login,go back to download page
  onSuccess() {
    this.show = false;
    this.ngZone.run(() => {
      //check if device udid id registered in current browser cache
      if (localStorage.getItem(this.fbConstants.localStorageItems.isDeviceRegistered) == null) {
        this.router.navigate([this.fbConstants.mob_router_links.detect_device_component],
          { queryParams: { build_id: this.build_id } });
      } else {
        this.router.navigate([this.fbConstants.mob_router_links.dashboard_component],
          { queryParams: { build_id: this.build_id } });
      }
    });
  }
}

