import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { NetworkService } from 'src/app/commonServices/network.service';
import { CurrentUser } from 'src/app/models/current-user';
import { FbConstants } from 'src/app/utils/fb-constants';

@Injectable({   providedIn: 'root' })
export class SettingsService {

  constructor( private fbApiConstants: FbApiConstants,
               private networkService: NetworkService,
               private fbConstants: FbConstants
             ) { }
  getProfileDetails(): Observable<CurrentUser> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.profile);
  }
  updateProfileDetails(params): Observable<CurrentUser> {
    return this.networkService.putRequest(params, this.fbApiConstants.API_URLS.profile);
  }
  updateNotificationStatus(params): Observable<any> {
    return this.networkService.postRequest(params,
           this.fbApiConstants.API_URLS.notifications);
  }
  getUserStorageDetails(): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.authtoken);
  }
  removeStorageAccess(storagType?: string): Observable<any> {
    const params = new FormData();
    params.append(this.fbConstants.build_info_keys.upload_type, storagType);
    return this.networkService.postRequest(params,
           this.fbApiConstants.API_URLS.storageRemove);
  }
  getStorageBuilds(storagType?: string): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.storageBuilds +
                                          storagType);
  }
  updateSurveyDetails(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.surveyDetails);
  }

  deleteProfileImage(params): Observable<any>{
    return this.networkService.putRequest(params, this.fbApiConstants.API_URLS.deleteProfileImage);
  }
}
