import { Component, OnInit, Input, OnChanges, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Apps } from 'src/app/models/apps';
import { FbConstants } from 'src/app/utils/fb-constants';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { DomSanitizer } from '@angular/platform-browser';
import { AppsService } from 'src/app/commonServices/apps.service';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { DeviceDetectorService } from 'ngx-device-detector';
import { environment } from '../../../environments/environment';
import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { DeviceRegistrationService } from '../../mobileAppServices/device-registration.service';
import { AuthService, RefreshTokenResult } from 'src/app/commonServices/auth.service';
import { CurrentUser } from 'src/app/models/current-user';
import { MatSnackBar } from '@angular/material/snack-bar';
import Swal from 'sweetalert2';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'app-applist',
  templateUrl: './applist.component.html',
  styleUrls: ['./applist.component.css']
})
export class ApplistComponent implements OnInit, OnChanges {
  @Input() appList: Apps[] = [];
  initialAppList = [];
  isEmptyList: boolean;
  label;
  share;
  platform;
  searchClicked: boolean;
  showNoResultsFound: boolean;
  searchForm = new UntypedFormGroup({
    search: new UntypedFormControl('')
  });

  downloadInProgress: boolean = false

  constructor(private route: ActivatedRoute, private router: Router, private sanitizer: DomSanitizer,
    public fbConstants: FbConstants, public fbApiConstants: FbApiConstants,
    private appsService: AppsService, private authService: FirebaseAuthService,
    private deviceService: DeviceDetectorService,
    private deviceRegistrationService: DeviceRegistrationService,
    private refreshAuthService: AuthService,
    private snackBar: MatSnackBar
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      const build_id = params['build_id'];
      
      if (build_id !== undefined) {
        this.router.navigate([this.fbConstants.mob_router_links.app_details_component],
          { queryParams: { build_id: build_id } });
      }
    });
  }
  ngOnChanges() {
    this.initialAppList = this.appList;
    this.clearSearch();
  }

  onClickApp(app: Apps) {
    this.label = app.version.bundle.label;
    this.share = app.share;
    this.platform = app.version.bundle.platform;
    this.router.navigate([this.fbConstants.mob_router_links.app_details_component],
      {
        queryParams: {
          'label': this.label,
          'share': this.share, 'platform': this.platform
        },
        queryParamsHandling: 'merge'
      }
    );
  }

  onClickDownloadBuild(app: Apps) {
    if (app.permission_flag !== this.fbConstants.permissions.public) {
      if (localStorage.getItem(this.fbConstants.localStorageItems.userInfo) !== null) {

        //If ios app and apple account is configured
        if (app.version.bundle.platform === this.fbConstants.platforms.iOS && app.version.bundle.appstore_profile_name != null) {

          //Check if this app is in list of app ids cached for udid registered from this device, else register device 
          const cachedApps: number[] = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.listOfUDIDAssociatedApps))

          if (cachedApps == null || !cachedApps.includes(app.version.bundle.id)) {
            sessionStorage.setItem(this.fbConstants.localStorageItems.appForUDID, String(app.version.bundle.id));
            sessionStorage.setItem(this.fbConstants.localStorageItems.buildForUDID, String(app.build_id));
            sessionStorage.setItem(this.fbConstants.localStorageItems.orgIdForUDID, String(app.organization.id));
            this.deviceRegistrationService.appInProgress = app;
            this.router.navigateByUrl(this.fbConstants.mob_router_links.detect_device_component);
            return
          }

        }

        const token = JSON.parse(localStorage.getItem(
          this.fbConstants.localStorageItems.userInfo)).data.json_token.access;
        if (!this.tokenExpired(token)) {
          const url =this.fbApiConstants.API_URLS.private_build_url +
            '?id=' + app.build_id + '&token=' + token;
            this.checkURLExists(url, app.version.bundle.platform); 
        } else {
          this.refreshAuthService.refreshToken().subscribe((result: RefreshTokenResult) => {
            const user: CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
            user.data.json_token.access = result.access;
            localStorage.setItem(this.fbConstants.localStorageItems.userInfo, JSON.stringify(user));
            const url =this.fbApiConstants.API_URLS.private_build_url +
            '?id=' + app.build_id + '&token=' + result.access;
            this.checkURLExists(url, app.version.bundle.platform); 
          }, (err) => {
            this.authService.logout();
          })
        }
      }
    } else {
        const url = this.fbApiConstants.API_URLS.public_build_url +
        '?id=' + app.build_id;
        this.checkURLExists(url, app.version.bundle.platform);
    }
  }

  showButton(app: Apps) {
    if(app.storage === null){
      app.infoString = this.fbConstants.tooltip.accessRevockedByAdmin;
      return true
    }
    if (app.storage.is_authenticated === null) {
      app.infoString = this.fbConstants.dialog_messages.inactive_build;
      return true;
    } else if (app.delete === null) {
      app.infoString = this.fbConstants.dialog_messages.inactive_build;
      return true;
    } else if (app.hidden === true) {
      app.infoString = this.fbConstants.dialog_messages.inactive_build;
      return true;
    } else if (this.isOSSupported(app)) {
      return true;
    } else {
      return false;
    }
  }
  isOSSupported(app: Apps) {
    let device_OS;
    const os_version = app.os_version;
    const deviceInfo = this.deviceService.getDeviceInfo();
    const isMobile = this.deviceService.isMobile();
    const isTablet = this.deviceService.isTablet();
    if (isMobile || isTablet) {
      if (deviceInfo.os === 'iOS' || deviceInfo.os === 'Mac') {
        device_OS = 1;
      } else if (deviceInfo.os === 'Android') {
        device_OS = 2;
      }
      if (device_OS !== app.version.bundle.platform) {
        app.infoString = this.fbConstants.dialog_messages.unsupported_os;
        return true;
      } else {
        app.infoString = 'Install';
        return false;
      }
    } else {
      app.infoString = this.fbConstants.dialog_messages.unsupported_os;
      return true;
    }
  }
  search(item) {
    this.showNoResultsFound = false;
    const appList = this.initialAppList.filter(appObj =>
      appObj.version.app_name.toLocaleLowerCase()
        .includes(item.toLocaleLowerCase()));
    if (appList.length === 0) {
      this.showNoResultsFound = true;
    }
    this.appList = appList;
  }
  clearSearch() {
    this.searchForm.reset();
    this.showNoResultsFound = false;
    this.searchClicked = false;
    this.appList = this.initialAppList;
  }
  checklength(length) {
    if (length === 0) {
      this.searchForm.reset();
      // this.searchClicked = false;
      this.showNoResultsFound = false;
      this.appList = this.initialAppList;
    }
  }
  private tokenExpired(token: string) {
    const expiry = (JSON.parse(atob(token.split('.')[1]))).exp;    
    return (Math.floor((new Date).getTime() / 1000)) >= expiry;
  }

  private checkURLExists(url : string, osType: number) {
    this.appsService.downloadBuild(url).subscribe(
      (result) => {
        if (result) {
          const downloadURL = environment.urls.baseUrl + url + '&start_download=true';
          this.downloadInProgress = true
          // asynchronous with BLOB like strategy.
          this.downloadFileWrap(downloadURL, osType).subscribe(()=> {
            this.downloadInProgress = false;
          }, () => {
            this.downloadInProgress = false;
          })
          this.snackBar.open(this.fbConstants.dialog_messages.downloading, '', {
            duration: 5000,
            panelClass: this.fbConstants.snackbarType.info
          });
        } 
      },
      (err)=>{
        Swal.fire('Oops!', err || err.message ||
        this.fbConstants.dialog_messages.commonServerError, 'error');
    })
  }

  downloadFileWrap(url: string, osType: number): Observable<Observable<boolean>> {
    if(osType === 1) {
      window.location.href = url
      this.downloadInProgress = false;
    return of(of(true))
    }
    return this.appsService.downloadFile(url)
  }


}
