import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { NetworkService } from 'src/app/commonServices/network.service';
import { Apps } from 'src/app/models/apps';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { FbConstants } from 'src/app/utils/fb-constants';

@Injectable({
  providedIn: 'root'
})
export class DeviceRegistrationService {

  public appInProgress: Apps;

  constructor(
    private fbApiConstants: FbApiConstants,
    private fbConstants: FbConstants,
    private networkService: NetworkService,
  ) { }

  submitDeviceRequest(udid: string, name: string, app: string, build: string, orgId: string): Observable<any> {
    const formData = new FormData();
    formData.append(this.fbConstants.udid_params.udid, udid);
    formData.append(this.fbConstants.udid_params.name, name);
    formData.append(this.fbConstants.udid_params.app, app);
    formData.append(this.fbConstants.udid_params.build, build);
    formData.append(this.fbConstants.udid_params.organization_id, orgId);
    return this.networkService.postRequest(formData, this.fbApiConstants.API_URLS.addUDID);
  }

}
