import { Component, OnInit, NgZone, Inject, Input } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/commonServices/login.service';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { FbConstants } from 'src/app/utils/fb-constants';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { NetworkService } from 'src/app/commonServices/network.service';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { User } from 'src/app/models/user';
import Swal from 'sweetalert2';
import { trimValidator } from 'src/app/utils/trim.validator';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  isFirstnameEmpty: boolean;
  isLastnameEmpty: boolean;
  isEmailEmpty: boolean;
  isPasswordEmpty: boolean;
  isEmptyField: boolean;

@Input() component = '';
params = new FormData();
constructor(private router: Router, public loginService: LoginService, private authService: FirebaseAuthService,
  public fbConstants: FbConstants,  public dialog: MatDialog, private loaderService: LoaderService,
  private zone: NgZone, private dialogRef: MatDialogRef<SignupComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
  private networkService: NetworkService ) {}
  signupForm = new UntypedFormGroup({
  email: new UntypedFormControl('', Validators.compose([
    Validators.required,
    Validators.pattern(this.fbConstants.patterns.email)
  ])),
  password : new UntypedFormControl('', [Validators.compose([
    Validators.required,
    Validators.pattern(this.fbConstants.patterns.string_without_space),
    Validators.minLength(6)
  ])]),
  firstName: new UntypedFormControl('', [Validators.required,
                                  Validators.pattern(this.fbConstants.patterns.string_without_space)]),
  lastName: new UntypedFormControl('', [Validators.required,
                                 Validators.pattern(this.fbConstants.patterns.string_without_space)])});
ngOnInit() {  }

// on clicking signup button: creation of new account
onClickSignUp(username: string, password: string) {
  this.isFirstnameEmpty = false;
  this.isLastnameEmpty = false;
  this.isEmailEmpty = false;
  this.isPasswordEmpty = false;

  if ( this.signupForm.value.firstName === '' || this.signupForm.value.lastName === '' ||
  username === '' || password === '') {
    this.isEmptyField = true;
  } else if (this.signupForm.value.firstName === '') {
  this.isFirstnameEmpty = true;
  } else if ( this.signupForm.value.lastName === '' ) {
  this.isLastnameEmpty = true;
  } else if ( username === '' ) {
  this.isEmailEmpty = true;
  } else if ( password === '' ) {
  this.isPasswordEmpty = true;
  } else {
    if ( this.signupForm.valid ) {
          this.loaderService.showLoader = true;
          this.authService.signUpWithEmail(username, password).then((result) => {
            this.authService.sendEmail().then((response) => {
                  const user: User = <User> {};
                  user.email = this.signupForm.value.email;
                  user.given_name = this.signupForm.value.firstName;
                  user.family_name = this.signupForm.value.lastName;
                  // user.picture = this.fbConstants.url_substrings.defaultIcon + this.fbConstants.icons.profile;
                  user.loginType = this.fbConstants.login_types.other;
                  // this.networkService.getIcon(user.picture).subscribe((data: any) => {
                  //   const fileName = (user.picture).split('/').pop();
                  //   const file  = new File([data], fileName );
                  //   this.params.append(this.fbConstants.user_details.profileImage, file, file.name);
                    this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
                      .user.stsTokenManager.accessToken, this.params)
                          .subscribe( res => {
                    if (res.data.json_token.access) {
                      this.loaderService.showLoader = false;
                      if (this.component === 'download_login') {
                        this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component]);
                      } else {
                        this.router.navigate([this.fbConstants.mob_router_links.signin]);
                      }
                    }
                  },
                  (err) => {
                    this.loaderService.showLoader = false;
                    this.zone.run(() => {
                    Swal.fire('Oops!', err.message ||
                    this.fbConstants.dialog_messages.commonServerError, 'error');
                    });
                    }
                  );
                  // });
                  // Swal.fire('Verify your email', this.fbConstants.dialog_messages.email_verification
                  // + this.fbConstants.dialog_messages.email_note, 'success')
                  Swal.fire({
                    title: 'Verify your email',
                    html:  this.fbConstants.dialog_messages.email_verification  +
                    this.fbConstants.dialog_messages.email_note ,
                    icon: 'success',
                    confirmButtonText: 'OK',
                  })
                  .then( res => {
                      // this.dialogRef.close();
                      this.onClickLogin();
                  }
                  );
                  }).catch(err => {
                    this.loaderService.showLoader = false;
                      if (this.component === 'download_login') {
                        this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component]);
                      } else {
                        this.router.navigate([this.fbConstants.mob_router_links.signin]);
                      }
                    });
                  },
                  (err) => {
                    this.loaderService.showLoader = false;
                    this.zone.run(() => {
                    Swal.fire('Oops!', err.message || this.fbConstants.dialog_messages.commonServerError, 'error');
                    });
            }).catch(error => {
              this.loaderService.showLoader = false;
              Swal.fire('Oops!', error.message, 'error');
            });
    } // if valid
}// else
}

// login button click: already existing account
onClickLogin() {
  this.loginService.isNewUser = false;
}
getErrorMessage() {
  return this.signupForm.controls['password'].hasError('required') ?  'This field is required' :
      this.signupForm.controls['password'].hasError('pattern') ? 'Whitespaces are not allowed' :
      this.signupForm.controls['password'].hasError('minlength') ? 'Password must contain at least 6 characters' :
         '';
}

}
