import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { FbConstants } from 'src/app/utils/fb-constants';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { CurrentUser } from 'src/app/models/current-user';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { LoginService } from 'src/app/commonServices/login.service';
import { Router } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { Device } from 'src/app/models/device';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  profile: CurrentUser;
  notification;
  checked: boolean;
  accountType: string;
  isNormalLogin: boolean;
  profileImage: string;
  profileName: string;
  profileEmail: any;
  initials: string;
  params = new FormData();
  color = this.fbConstants.colorCode.defaultHex;
  hasRegisteredDevices: boolean;
  constructor( private fbConstants: FbConstants, public fbApiConstants: FbApiConstants,
               private authService: FirebaseAuthService, private loginService: LoginService,
               private router: Router, private afAuth: AngularFireAuth ) { }

  ngOnInit() {
    this.loadProfile();
    this.checkIfUserHasRegisteredDevice();
  }
  private loadProfile() {
      this.profile = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
      this.profileImage = this.profile.data.profile_image;
      this.profileName = this.profile.data.firstName;
      this.initials = this.profile.data.firstName;
      this.profileEmail = this.profile.data.email;
      this.accountType = this.profile.plan;
  }

  private checkIfUserHasRegisteredDevice() {
    const currentUser: CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));    
    let devices:Device[] = currentUser.data.udid_list;
    this.hasRegisteredDevices = devices.length > 0;
  }
  onClickLogout() {
    this.authService.logout().then((res) => {
      this.loginService.userLogout().subscribe( response => {
        if (response.message) {
          // this.router.navigate([this.fbConstants.web_router_links.signin]);
          window.location.href=environment.urls.baseUrl
          localStorage.removeItem(this.fbConstants.localStorageItems.userInfo);
          localStorage.removeItem(this.fbConstants.localStorageItems.login_type);
          localStorage.removeItem(this.fbConstants.localStorageItems.plan_type);
          localStorage.removeItem('current_authorization');
          localStorage.clear();

        }
      }, (err) => {
        Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
      });
    });
}

  onListMyDevice() {
    this.router.navigate([this.fbConstants.mob_router_links.my_devices])
  }

}
