import { Component, OnInit, Output, EventEmitter, OnChanges } from "@angular/core";
import { MatDialog } from '@angular/material/dialog';
import { TryItNowComponent } from '../../try-it-now/try-it-now.component';
import { SigninComponent } from '../../signin/signin.component';
import { FbConstants } from 'src/app/utils/fb-constants';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: "app-landing-header",
  templateUrl: "./landing-header.component.html",
  styleUrls: ["./landing-header.component.css"]
})
export class HeaderComponent implements OnInit,OnChanges {

  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    throw new Error("Method not implemented.");
  }

  isDialogueDisplayed:boolean = false;

  constructor(public fbConstants: FbConstants,private dialog: MatDialog, private route :ActivatedRoute, private router :Router) { }

  ngOnInit() { }

  scrollTo(className: string): void {
    const elementList = document.querySelectorAll('.' + className);
    const element = elementList[0] as HTMLElement;
    element.scrollIntoView({ behavior: 'smooth' });
  }

  onClickSignUp() {
    this.isDialogueDisplayed = true
    this.dialog.open(SigninComponent, {data:
      {login_type: this.fbConstants.login_types.other},
      width: '800px', panelClass: 'custom-dialog-container',
      autoFocus: false, disableClose: true}).afterClosed().subscribe(result => {
        this.isDialogueDisplayed = false;
      });
  }
//   onClickSignUp() {
//     this.route.queryParams.subscribe( params => {
//       const build_id = params['build_id'];
//       if (build_id !== undefined) {
//         this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component],
//         {queryParams: {build_id: build_id}});
//       }
//       this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component]);
//   });
// }

  onClickHowToUse() {
    this.isDialogueDisplayed = true
    this.dialog.open(TryItNowComponent, {
      data: { mode: 'demo-video' },
      width: '600px', disableClose: false,
      panelClass: 'custom-dialog-container'
    }).afterClosed().subscribe(result => {
      this.isDialogueDisplayed = false;
    });
  }

}
