import { Component, OnInit, NgZone } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AppsService } from 'src/app/commonServices/apps.service';
import { Apps } from 'src/app/models/apps';
import { DeviceDetectorService } from 'ngx-device-detector';
import { MatDialog } from '@angular/material/dialog';
import { FbConstants } from 'src/app/utils/fb-constants';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { LoginService } from 'src/app/commonServices/login.service';
import { DomSanitizer } from '@angular/platform-browser';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import Swal from 'sweetalert2';
import { Team } from 'src/app/models/team';

@Component({
  selector: 'app-download-page',
  templateUrl: './download-page.component.html',
  styleUrls: ['./download-page.component.css']
})

export class DownloadPageComponent implements OnInit {
  show: boolean;
  isLoggedIn;
  build_id;
  deviceInfo = null;
  device_OS;
  OS;
  appInfo: Apps;
  os_version;
  hidden;
  delete;
  downloadUrl;
  permisson_flag: string;
  userEmail;
  teamArray: Team[] = [];
  emailList: string[] = [];
  usersList = [];
  btnEnabled: boolean;
  platform;

  constructor(private activatedRoute: ActivatedRoute, private appsService: AppsService, private dialog: MatDialog,
              private deviceService: DeviceDetectorService, private authService: FirebaseAuthService,
              public fbConstants: FbConstants, public fbApiConstants: FbApiConstants, private router: Router,
              private loginService: LoginService, private zone: NgZone, private sanitizer: DomSanitizer) { }

  ngOnInit() {
  //  this.getAppDetails();
  //  this.userEmail = this.authService.email;
  }

 // getting details of current build
  private getAppDetails() {
     this.activatedRoute.queryParams.subscribe( params => {
        this.build_id = params['build_id'] || '';
        // this.isLoggedIn = params['isLoggedIn'] || '';
        if (this.build_id === '') {
           this.detectDevice();
        } else {
          this.show = true;
          this.appsService.getDetails(this.build_id).subscribe( result => {
           // this.show = false;
            this.appInfo = result;
            this.hidden = this.appInfo.hidden;
            this.os_version = this.appInfo.os_version;
            this.teamArray = this.appInfo.team_list;

            this.teamArray.forEach(element => {
              for (let i = 0 ; i < element.users.length; i++) {
                this.usersList.push(element.users[i]);
              }
            });
            this.emailList = removeDuplicateUsingFilter(this.usersList);
            function removeDuplicateUsingFilter(arr) { // avoiding duplication of users using filter method
              const unique_array = arr.filter(function(elem, index, self) {
                  return index === self.indexOf(elem);
              });
              return unique_array;
          }
            this.delete = this.appInfo.delete;
            if (this.appInfo.version.bundle.platform === this.fbConstants.platforms.android) {
              this.platform = 'Android';
            } else {
              this.platform = 'iOS';
            }
            this.detectDevice();
            // this.epicFunction(); original code
            this.show = false;

            if (this.delete === true) {
              this.show = false;
                Swal.fire('Something went wrong!', this.fbConstants.dialog_messages.download_build_deleted, 'error');
            } else {
                // this.show = false;
                // if (this.isLoggedIn) {
                //     const isUserExists = (this.emailList.find(x => x === this.userEmail)) ||
                //                          (this.appInfo.uploader.email === this.userEmail);
                //     if (isUserExists) {
                //       this.btnEnabled = true;
                //     } else {
                //       this.btnEnabled = false;
                //       Swal.fire('Something went wrong!', this.fbConstants.dialog_messages.download_access_denied, 'error');
                //     }
                //   }
             }

            if (this.appInfo.version.bundle.platform === this.fbConstants.platforms.android) {
              this.downloadUrl = this.appInfo.url_link;
            } else {
               this.downloadUrl = this.sanitizer.bypassSecurityTrustUrl(this.fbApiConstants.getDownloadUrl(this.build_id));
            }
        },
       (err) => {
        this.zone.run(() => {
          this.show = false;
          Swal.fire('Oops!', err || err.message ||
                this.fbConstants.dialog_messages.commonServerError, 'error');
          });
       }
     );
        }
    });
  }

// OS check
  private detectDevice() {
      this.deviceInfo = this.deviceService.getDeviceInfo();
      const isMobile = this.deviceService.isMobile();
      if (isMobile) {
          if ( this.deviceService.getDeviceInfo().os === 'iOS') {
            this.device_OS = 1;
          } else if (this.deviceService.getDeviceInfo().os === 'Android') {
            this.device_OS = 2;
          }
          if (this.device_OS === this.appInfo.version.bundle.platform) {
               this.show = false;
          } else {
            Swal.fire(this.fbConstants.dialog_messages.unsupported_os,
              this.fbConstants.dialog_messages.unsupported_os_message + this.platform +
              ' device of version '  + this.os_version + ' and above', 'warning');
            this.OS = false;
            this.router.navigate([this.fbConstants.mob_router_links.signin]);
          }

          if (this.deviceInfo.os_version < this.os_version) {
            Swal.fire(this.fbConstants.dialog_messages.unsupported_os,
              this.fbConstants.dialog_messages.unsupported_os_message + this.platform +
              ' device of version ' + this.os_version + ' and above', 'warning');
            this.OS = false;
          } else {
            this.OS = true;
          }
      } else {
        Swal.fire(this.fbConstants.dialog_messages.unsupported_os,
          this.fbConstants.dialog_messages.unsupported_os_message + this.platform +
          ' device of version ' + this.os_version + ' and above', 'warning');
             this.OS = false;
      }
  }

// popup for unsupported OS
  // onClickUnsupportedOS() {
  //   Swal.fire('Alert', this.fbConstants.dialog_messages.supporting_platform + this.os_version + ' and above', 'warning');
  // }

// downloads the current build
  onClickDownloadBuild(build_id) {
    // if (this.appInfo.permission_flag === 'public') {
    // this.appsService.informDownload(build_id);
    //   this.router.navigate([this.fbConstants.mob_router_links.signin]);
    // }
     if (this.appInfo.permission_flag !== this.fbConstants.permissions.public) {
        this.appsService.informDownload(build_id).subscribe( result => {
         this.authService.logout().then(res => { // firebase logout then flybuilds logout
            this.loginService.userLogout().subscribe( response => {
                if (response.message) {
                    this.authService.logout();
                    localStorage.removeItem(this.fbConstants.tokens.access_token);
                    localStorage.removeItem(this.fbConstants.localStorageItems.userInfo);
                    localStorage.removeItem(this.fbConstants.localStorageItems.login_type);
                    localStorage.clear()
                }
            },
            (err) => {
              this.zone.run(() => {
                Swal.fire('Oops!', err || err.message ||
                this.fbConstants.dialog_messages.commonServerError, 'error');
                });
            });
          }).catch( err => {
              this.zone.run(() => {
                Swal.fire('Oops!', err || err.message ||
                this.fbConstants.dialog_messages.commonServerError, 'error');
                });
            });
          },
          (err) => {
            this.zone.run(() => {
              Swal.fire('Oops!', err || err.message ||
              this.fbConstants.dialog_messages.commonServerError, 'error');
              });
          }
        );
       }
  }

// go to download page login when the build is private
  loginToDownload() {
    this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component],
       { queryParams: { build_id: this.build_id }});
  }
}
