import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { ChildState, LoaderState } from '../models/loader';


@Injectable({   providedIn: 'root' })

export class LoaderService {
    showLoader = false;

    private childSubject = new Subject<ChildState>();
    childState = this.childSubject.asObservable();

    private loaderSubject = new Subject<LoaderState>();
    loaderState = this.loaderSubject.asObservable();

    constructor() { }
    show() {
        this.childSubject.next({showChild: true} as ChildState);
    }
    hide() {
        this.childSubject.next({showChild: false} as ChildState);
        this.childSubject.next({showChild: false} as ChildState);
    }
    startLoader() {
        this.loaderSubject.next({show: true} as LoaderState);
    }
    endLoader() {
        this.loaderSubject.next({show: false} as LoaderState);
    }
}
