import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Apps } from 'src/app/models/apps';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { FbConstants } from 'src/app/utils/fb-constants';


@Component({
  selector: 'app-buildlist',
  templateUrl: './buildlist.component.html',
  styleUrls: ['./buildlist.component.css']
})
export class BuildlistComponent implements OnInit {
  @Input() appsList: Apps[] = [];
  @Output() appInfo = new EventEmitter<Apps>();
  constructor(public fbApiConstants: FbApiConstants, public fbConstants: FbConstants) { }

  ngOnInit() {
  }
  onClickApp(app) {
    this.appInfo.emit(app);
  }
}
