import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SettingsService } from 'projects/web-app/src/app/webAppServices/settings.service';
import { CurrentUser } from 'src/app/models/current-user';
import { FbConstants } from 'src/app/utils/fb-constants';
import Swal, { SweetAlertIcon } from 'sweetalert2';
import { DeviceRegistrationService } from '../../../mobileAppServices/device-registration.service';

@Component({
  selector: 'app-device-processing',
  templateUrl: './device-processing.component.html',
  styleUrls: ['./device-processing.component.css']
})
export class DeviceProcessingComponent implements OnInit {

  public headingText = "";
  public subheadingText = "";
  public isLoading = false;
  public apiResultState;

  constructor(
    private router: Router,
    private fbConstants: FbConstants,
    private activatedRoute: ActivatedRoute,
    private deviceRegistrationService: DeviceRegistrationService,
    private settingsService: SettingsService,
  ) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      let udid = params['udid'];
      let name = params['product'];
      const appId = sessionStorage.getItem(this.fbConstants.localStorageItems.appForUDID)
      const buildId = sessionStorage.getItem(this.fbConstants.localStorageItems.buildForUDID)
      const orgId = sessionStorage.getItem(this.fbConstants.localStorageItems.orgIdForUDID)
      var deviceName = this.fbConstants.modelToDevice[name];
      if (deviceName == null) {
        deviceName = name;
      }      
      if (udid != null) {
        this.isLoading = true;
        this.deviceRegistrationService.submitDeviceRequest(udid,deviceName,appId,buildId,orgId).subscribe(result => {
          this.isLoading = false;
          if (result.message === "ok") {
            Swal.fire(this.fbConstants.dialog_titles.device_registered, this.fbConstants.dialog_messages.device_register,
              this.fbConstants.dialog_type.success as SweetAlertIcon);
            this.headingText =  this.fbConstants.bodyTitle.deviceRegistrationSuccess;
            this.subheadingText = this.fbConstants.bodySubTitle.deviceRegistrationSuccess
            
            //Remove cached device registration details
            sessionStorage.removeItem(this.fbConstants.localStorageItems.appForUDID);
            sessionStorage.removeItem(this.fbConstants.localStorageItems.buildForUDID);
            sessionStorage.removeItem(this.fbConstants.localStorageItems.orgIdForUDID);

            //Set session storage about device registered
            var cachedAppIds:number[] = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.listOfUDIDAssociatedApps));
            if (cachedAppIds != null) {
              cachedAppIds.push(Number(appId))
            } else {
              cachedAppIds = [Number(appId)]
            }
            localStorage.setItem(this.fbConstants.localStorageItems.listOfUDIDAssociatedApps, JSON.stringify(cachedAppIds));
            
            this.settingsService.getProfileDetails().subscribe(userResult => {
              if (userResult != null) {
                const user:CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
                user.data.udid_list = userResult.data.udid_list
                localStorage.setItem(this.fbConstants.localStorageItems.userInfo, JSON.stringify(user));
              }
            })

          } else {
            Swal.fire(this.fbConstants.dialog_titles.device_register_failed, this.fbConstants.dialog_messages.device_register_fail,
              this.fbConstants.dialog_type.error as SweetAlertIcon);
            this.headingText = this.fbConstants.bodyTitle.deviceRegistrationFail;
            this.subheadingText = this.fbConstants.bodySubTitle.deviceRegistrationFail;
          }
        });
      }
    });
  }

  goToHome() {
    this.router.navigate([this.fbConstants.mob_router_links.dashboard_component])
  }

}
