import { Injectable } from '@angular/core';
import 'firebase/auth';
import { FbConstants } from '../utils/fb-constants';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import firebase from 'firebase/compat/app';

@Injectable({   providedIn: 'root' })
export class FirebaseAuthService {
  email: string;
  constructor( private fbConstants: FbConstants,
               private afAuth: AngularFireAuth
             ) {
                  afAuth.authState.subscribe((currentUser) => {
                      if (currentUser) {
                        const userDetails = currentUser;
                        this.email = userDetails.email;
                      }
                  });
                }
  getAuth() { //  returns the auth API that AngularFireAuth wraps
     return this.afAuth;
  }
  doGoogleLogin(): Promise<any> {
    const provider = new firebase.auth.GoogleAuthProvider();
    provider.addScope(this.fbConstants.googleAuthentication.profile_scope);
    provider.setCustomParameters({
      prompt: 'select_account',
      access_type: 'offline'
    });
    return firebase.auth().signInWithPopup(provider);
}

doFacebookLogin(): Promise<any> {
  const provider = new firebase.auth.FacebookAuthProvider();
  provider.setCustomParameters({
    auth_type: 'reauthenticate'
  });
  return firebase.auth().signInWithPopup(provider);
}
// login with email and password
loginWithEmail(email: string, password: string) {
    return this.afAuth.signInWithEmailAndPassword(email, password);
}
// user sign up
signUpWithEmail(email: string, password: string): Promise<any> {
  return this.afAuth.createUserWithEmailAndPassword(email, password);
}
sendEmail() {
  const user: any = firebase.auth().currentUser;
  return user.sendEmailVerification();
}
// sends the password reset request to Firebase which will construct the email.
resetPassword(email: string) {
  return firebase.auth().sendPasswordResetEmail(email);
}
logout() {
  return firebase.auth().signOut();
}
signinWithCredential(token) {
   const credential = firebase.auth.GoogleAuthProvider.credential(null, token);
   return firebase.auth().signInWithCredential(credential);
}
handleMultipleProviderError(error) {
  firebase.auth().signInWithCredential(error.credential).then( result => {
  });
}
}
