import { Component, HostListener, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FbConstants } from 'src/app/utils/fb-constants';

@Component({
  selector: 'app-base',
  templateUrl: './base.component.html',
  styleUrls: ['./base.component.css']
})
export class BaseComponent implements OnInit {

  constructor(
    private fbConstants: FbConstants,
    private router: Router,
  ) { }

  ngOnInit() {
    this.tabOpenAction();
  }

  tabOpenAction() {

    //if coming from login page skip this method
    let isNavigatedFromLogin: boolean = JSON.parse(sessionStorage.getItem(this.fbConstants.sessionStorageItems.isNavigatedFromLogin));

    if (this.checkIfSessionTabExist()) {
      //Restore session tab info if page reloaded
      this.restoreSessionTabIfPageReloaded()
    } else {
      //Fetch all local tabs info
      let localStorageTabs: number[] = [];
      localStorageTabs = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.tabs));

      //check if active tabs exist
      if (localStorageTabs != null && localStorageTabs.length > 0) {

        //If other tabs exist
        this.registerNewTabInfo();

      } else {

        //If it is fresh tab check remember me
        let rememberMe = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.rememberMyLogin));

        if (rememberMe || isNavigatedFromLogin) {
          // If remember me enabled register tab info
          this.registerNewTabInfo();
          sessionStorage.setItem(this.fbConstants.sessionStorageItems.isNavigatedFromLogin, JSON.stringify(false));
        } else {
          // else end user session
          this.endUserSession();
        }

      }
    }

  }

  checkIfSessionTabExist(): boolean {
    const currentSessionTab: number = Number(sessionStorage.getItem(this.fbConstants.sessionStorageItems.tabID));
    if (currentSessionTab !== 0) {
      return true;
    }
    return false;
  }

  restoreSessionTabIfPageReloaded() {
    //If session tab info is active in case of page reload => retore session tab info to localstorage
    //Fetch current session tab info
    const currentSessionTab: number = Number(sessionStorage.getItem(this.fbConstants.sessionStorageItems.tabID));
    if (currentSessionTab !== 0) {
      //Restore current session tab to localstorage
      let localStorageTabs: number[] = [];
      localStorageTabs = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.tabs));
      if (localStorageTabs == null) {
        localStorageTabs = [];
      }
      localStorageTabs.push(currentSessionTab);
      localStorage.setItem(this.fbConstants.localStorageItems.tabs, JSON.stringify(localStorageTabs));
    }
  }

  registerNewTabInfo() {
    //Register new session tab info
    const tabId = new Date().getTime();
    sessionStorage.setItem(this.fbConstants.sessionStorageItems.tabID, JSON.stringify(tabId));

    //Register session tab in localstorage
    let localStorageTabs: number[] = [];
    localStorageTabs = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.tabs));
    if (localStorageTabs == null) {
      localStorageTabs = [];
    }
    localStorageTabs.push(tabId);
    localStorage.setItem(this.fbConstants.localStorageItems.tabs, JSON.stringify(localStorageTabs));
  }

  UnregisterCurrentTabInfo() {
    //Fetch current session tab info
    const currentSessionTab: number = Number(sessionStorage.getItem(this.fbConstants.sessionStorageItems.tabID));

    //Fetch all local tabs info
    let localStorageTabs: number[] = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.tabs));

    //Remove current session tab info from local tabs
    localStorageTabs.forEach((element, index) => {
      if (element === currentSessionTab) localStorageTabs.splice(index, 1);
    });
    localStorage.setItem(this.fbConstants.localStorageItems.tabs, JSON.stringify(localStorageTabs));

  }

  endUserSession() {
    localStorage.removeItem(this.fbConstants.localStorageItems.userInfo);
    localStorage.removeItem(this.fbConstants.localStorageItems.login_type);
    localStorage.removeItem(this.fbConstants.localStorageItems.plan_type);
    localStorage.removeItem('current_authorization');
    localStorage.clear();
    this.router.navigate([this.fbConstants.web_router_links.signin]);
  }

  @HostListener('window:beforeunload ', ['$event'])
  unloadHandler(event) {
    //On tab close unregister tab info from session and tab
    this.UnregisterCurrentTabInfo()
  }

}
