import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PreloadAllModules } from '@angular/router';
import { LandingPageComponent } from './components/landing-page/landing-page.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { UserAuthComponent } from './components/user-auth/user-auth.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { AuthGuardService } from '../app/mobileAppServices/auth-guard.service';
import { DownloadPageComponent } from './components/download-page/download-page.component';
import { AppdetailsComponent } from './components/appdetails/appdetails.component';
import { LoginComponent } from './components/login/login.component';
import { PrivacyPolicyComponent } from './components/privacy-policy/privacy-policy.component';
import {ApplistComponent} from './components/applist/applist.component'
import { VerifyOwnerComponent } from './components/verify-owner/verify-owner.component';
import { AddDeviceComponent } from './components/add-device/add-device.component';
import { MyDevicesComponent } from './components/my-devices/my-devices/my-devices.component';
import { DeviceProcessingComponent } from './components/device-processing/device-processing/device-processing.component';
import { BaseComponent } from './components/base/base.component';

const routes: Routes = [
  { path : '', redirectTo: '/home', pathMatch: 'full' },
  { path: '.', component: LandingPageComponent },
  { path: 'login', component: LandingPageComponent},
  {
    path: '',
    component: BaseComponent,
    children: [
      { path: 'home', component: DashboardComponent, canActivate: [AuthGuardService] },
      { path: 'appdetail', component: AppdetailsComponent },
      { path: 'download', component: DownloadPageComponent, canActivate: [AuthGuardService] },
      { path: 'details', component: AppdetailsComponent },
      { path: 'detect-device', component: AddDeviceComponent, canActivate: [AuthGuardService] },
      { path: 'device-processing', component: DeviceProcessingComponent, canActivate: [AuthGuardService] },
      { path: 'my-devices', component: MyDevicesComponent, canActivate: [AuthGuardService] },
    ]
  },
  
  { path: 'user-auth', component: UserAuthComponent },
  // { path: 'downloadlogin', component: DownloadPageLoginComponent },
  { path: 'privacy-policy', component: PrivacyPolicyComponent },
  { path: 'verify', component: VerifyOwnerComponent},
  { path: 'team', component: DashboardComponent, canActivate: [AuthGuardService] },
  { path : '**', component : PageNotFoundComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes,
    {
    preloadingStrategy: PreloadAllModules,
    relativeLinkResolution: 'legacy'
}) ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
