import { Component, Input, OnInit, ViewChild, NgZone } from '@angular/core';
import { AppsService } from 'src/app/commonServices/apps.service';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { AppComment } from 'src/app/models/app-comment';
import { Apps } from 'src/app/models/apps';
import { CurrentUser } from 'src/app/models/current-user';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { FbConstants } from 'src/app/utils/fb-constants';
import Swal, { SweetAlertIcon } from 'sweetalert2';

@Component({
  selector: 'app-app-comments',
  templateUrl: './app-comments.component.html',
  styleUrls: ['./app-comments.component.css']
})
export class AppCommentsComponent implements OnInit {

  @Input() items: Apps[] = [];
  @Input() appComments: AppComment[] = [];
  @Input() selectedApp: Apps;

  @ViewChild('fileInput')
  public fileInput;

  loggedInUser: CurrentUser;
  commentOpenState = false;
  initials: string;
  selectedReleaseForComment: number;
  commentFilter: number;
  newestFirst: string;
  comments = [];
  filterOptions = [];
  isFiltered = false;
  filteredReleaseNumber: number;
  attachmentUnderEdit = []
  selectedFiles: FileList;
  commentString: string;
  sendingComment: boolean;
  deletingIndex: number;
  editingIndex: number;
  commentPostErrorText: string;
  mainActionTitle = "Submit"
  editingComment: string;
  commentReleaseNumber: number;
  isEditingComment = true;
  accept = this.fbConstants.extensions.imageAccept;
  isAttachmentUploaded = false;

  constructor(
    private fbConstants: FbConstants,
    private appsService: AppsService,
    private authService: FirebaseAuthService,
    private zone: NgZone,
    public fbApiConstants: FbApiConstants,
  ) { }

  ngOnInit() {
    this.loggedInUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
    if (!!this.loggedInUser) {
      this.initials = this.loggedInUser.data.firstName.charAt(0)
        .toUpperCase() +
        this.loggedInUser.data.firstName.slice(1)
          .toLowerCase();
    }
  
  }

  ngOnChanges() {
    this.comments = this.appComments;
    
    if (this.items.length > 0) {
      
      this.selectedReleaseForComment = this.items[0].release_number;
      this.newestFirst = 'newestFirst'
    }
    if (this.appComments != null) {
      this.filterDropdown();
      
      this.appComments.forEach(appComment => {
        this.downloadAttachments(appComment);
      });
    }
  }

  public filterDropdown(){
    
    this.filterOptions = this.appComments.map((value)=> value.release_number).filter((value, index, self) => self.indexOf(value) === index).sort((a,b)=> a-b);
  }

  private downloadAttachments(appComment: AppComment) {
    var downloadedAttachments = [];
    var downloadedAttachmentsBase64 = [];
    appComment.attachments.forEach(attachmentId => {
      this.appsService.getAttachment(attachmentId).subscribe(attachment => {
        let thumbnail = this.fbConstants.srcType.attachment + attachment.base64;
        downloadedAttachments.push(thumbnail);

        let base64Image = attachment.base64;
        downloadedAttachmentsBase64.push(base64Image);
      });
    });
    appComment.downloadedAttachments = downloadedAttachments;
    appComment.downloadedAttachmentsBase64 = downloadedAttachmentsBase64;
  }

  public onDownloadAction(index: number, appComment: AppComment) {
    const blobData = this.convertBase64ToBlobData(appComment.downloadedAttachmentsBase64[index]);
    const blob = new Blob([blobData], { type: this.fbConstants.srcType.png });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = this.fbConstants.stringLiterals.attachmentName;
    link.click();
  }

  public onCommentFocusAction() {
    //Disable comment permission for non-logged in users
    if (this.loggedInUser == null) { return }
    this.isAttachmentUploaded = false;
    this.commentOpenState = !this.commentOpenState;
    if (!this.commentOpenState) {
      this.resetToInitialState();
       this.isEditingComment = true;
    }
    this.attachmentUnderEdit = null;
    this.commentPostErrorText = null;
   
  }

  public changeClient(event) {
    this.selectedReleaseForComment = event.source.value;
    if(this.commentReleaseNumber === this.selectedReleaseForComment){
      this.isEditingComment= true;
    }else{
      this.isEditingComment= false;
    }
  }

  public changeFilter(event){
    this.isFiltered = false;
    this.filteredReleaseNumber = event.value;
    if(event.value === 'newestFirst'){
      this.isFiltered = false;
      this.comments = this.appComments;
    }
    else{
      this.isFiltered = true;
      let commentFilters = this.appComments.filter(comment => comment.release_number === event.value);
      this.comments = commentFilters;
    }
  }

  public selectFiles(event) {
    //If editing in progress upload attachment directly
    if (this.editingIndex != null) {
      
      let commentUnderEdit;
      if(this.isFiltered === false){
        
        commentUnderEdit = this.appComments[this.editingIndex];
        
      }else{
        commentUnderEdit = this.comments[this.editingIndex];
      }
      this.sendingComment = true;
      this.appsService.updateAttachment(commentUnderEdit.id,event.target.files).subscribe(res => {
        const files = res.files
        this.commentPostErrorText = null;
        files.forEach(file => {
          let thumbnail = 'data:image/png;base64,' + file.base64;
          let base64Image;
          if(this.isFiltered === false){
        
            this.appComments[this.editingIndex].downloadedAttachments.push(thumbnail);

            base64Image = file.base64;
            this.appComments[this.editingIndex].downloadedAttachmentsBase64.push(base64Image);

            this.appComments[this.editingIndex].attachments.push(file.id);
          }else{
            this.comments[this.editingIndex].downloadedAttachments.push(thumbnail);

            base64Image = file.base64;
            this.comments[this.editingIndex].downloadedAttachmentsBase64.push(base64Image);

            this.comments[this.editingIndex].attachments.push(file.id);
          }
          this.isAttachmentUploaded = true;
        }); 
        this.sendingComment = false;
      }, (error) => {
        this.isEditingComment = true;
        this.sendingComment = false;
        this.commentPostErrorText = error;
        this.isAttachmentUploaded = false;
      })
    } else {
      this.selectedFiles = event.target.files;
      this.isAttachmentUploaded = false;
    }
  }

  public onClickFileInputButton(): void {
    this.isAttachmentUploaded = false;
    this.isAttachmentUploaded = false;
    this.fileInput.nativeElement.click();
  }

  public onCommentAction() {
    this.commentPostErrorText = null;
    this.sendingComment = true;

    //If editing
    if (this.editingIndex != null) {
      let appCommentUnderEdit;
      if(this.isFiltered === false){
        
         appCommentUnderEdit = this.appComments[this.editingIndex];
      }else{
        appCommentUnderEdit = this.comments[this.editingIndex];
      }
      
      this.appsService.updateComment(
        this.selectedApp.version.bundle.id,
        this.getBuildNumberOfSelectedRelease(),
        this.commentString,
        this.selectedFiles,
        appCommentUnderEdit.id
      ).subscribe(res => {
        this.commentOpenState = false
        this.sendingComment = false;
        if (res.data != null) {
          this.commentString = "";
          if(this.isFiltered === false){
            const found = this.appComments.find(element => element.id === res.data.id);
            this.appComments[this.editingIndex] = res.data
            this.downloadAttachments(this.appComments[this.editingIndex]);
          } else{
            const found = this.appComments.findIndex(element => element.id === res.data.id);
            this.appComments[found] = res.data;
            this.comments[this.editingIndex] = res.data;
            this.downloadAttachments(this.appComments[found]);
            this.downloadAttachments(this.comments[this.editingIndex]);
          }
          this.clearAttachments();
          this.editingIndex = null;
        }
         this.isEditingComment = true;
      }, (error) => {
         this.isEditingComment = true;
        this.sendingComment = false;
        this.commentPostErrorText = error;
      });
    } else {
      this.appsService.postComments(
        this.selectedApp.version.bundle.id,
        this.getBuildNumberOfSelectedRelease(),
        this.commentString,
        this.selectedFiles,
      ).subscribe(res => {
        this.commentOpenState = false
        this.sendingComment = false;
        if (res.data != null) {
          this.commentString = "";
          this.appComments.unshift(res.data);
          if(this.isFiltered === true){
            if(this.filteredReleaseNumber === res.data.release_number){
            this.comments.unshift(res.data);
            }
          }
          this.downloadAttachments(this.appComments[0]);
          this.clearAttachments();
          this.filterDropdown();
        }
         this.isEditingComment = true;
      }, (error) => {
         this.isEditingComment = true;
        this.sendingComment = false;
        this.commentPostErrorText = error;
      });
    }
  }

  public hasPermissionToDeleteComment(appComment: AppComment): boolean {
    return this.isVerifiedAdmin(this.selectedApp)
      || appComment.created_by.id == Number(this.loggedInUser.data.id);
  }

  public deleteComment(index: number) {
    this.isAttachmentUploaded = false;
    this.openConfirmationDialog(index);
  }

  openConfirmationDialog(index: number) {

    Swal.fire({
      title: this.fbConstants.dialog_titles.confirm,
      html: this.fbConstants.dialog_messages.delete_confirmation
        + '?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: this.fbConstants.dialog_button_text.yes,
      cancelButtonText: this.fbConstants.dialog_button_text.no,
      reverseButtons: true
    }).then((result) => {
      if (result.value) {
        this.deletingIndex = index;
        let appCommentUnderDelete
        if(this.isFiltered === false){
          appCommentUnderDelete = this.appComments[index].id;
          
        }else{
          appCommentUnderDelete = this.comments[index].id;
          
        }
        this.appsService.deleteAppComment(appCommentUnderDelete).subscribe(res => {
          this.deletingIndex = null;

        if(this.isFiltered === false){
          this.appComments.splice(index, 1);
        }else{
          
          const found = this.appComments.findIndex(element => element.id === appCommentUnderDelete);
          this.appComments.splice(found, 1);
          
          this.comments.splice(index, 1);
        }
          this.filterDropdown();
        },
        (err) => {
          this.deletingIndex = null;
          this.zone.run(() => {
          Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError,
          'error');
         }
         );
        });
      }
    });
  }

  public deleteCommentAttachment(index: number) {
    let appCommentUnderEdit;
    let attachmentId;
    if(this.isFiltered === false){
        
      appCommentUnderEdit = this.appComments[this.editingIndex];
      attachmentId = appCommentUnderEdit.attachments[index];
      this.sendingComment = true;
      this.appsService.deleteCommentAttachment(attachmentId).subscribe(res => {
        this.appComments[this.editingIndex].downloadedAttachments.splice(index,1);
        this.appComments[this.editingIndex].downloadedAttachmentsBase64.splice(index,1);
        this.appComments[this.editingIndex].attachments.splice(index,1);
        this.sendingComment = false;
    })
   }else{
     
      appCommentUnderEdit = this.comments[this.editingIndex];
      attachmentId = appCommentUnderEdit.attachments[index];
      this.sendingComment = true;
      this.appsService.deleteCommentAttachment(attachmentId).subscribe(res => {
        this.comments[this.editingIndex].downloadedAttachments.splice(index,1);
        this.comments[this.editingIndex].downloadedAttachmentsBase64.splice(index,1);
        this.comments[this.editingIndex].attachments.splice(index,1);
        this.sendingComment = false;
    })
   }
   
  }

  public editCommentAction(index: number) {
    document.getElementById("comment-list").scrollIntoView({
      behavior: "smooth"
    });
    if(this.isFiltered === false){
      this.editingIndex = index;
      this.isAttachmentUploaded = false;
      this.commentOpenState = true;
      this.commentString = this.appComments[index].content;
      this.editingComment = this.appComments[index].content;
      this.selectedReleaseForComment = this.appComments[index].release_number;
      this.commentReleaseNumber = this.appComments[index].release_number;
      this.attachmentUnderEdit = this.appComments[index].downloadedAttachments;
      this.mainActionTitle = this.fbConstants.buttonTitles.update;
    } 
    else{
      this.editingIndex = index;
    
      this.commentOpenState = true;
      this.commentString = this.comments[index].content;
    
      this.editingComment = this.comments[index].content;
      this.selectedReleaseForComment = this.comments[index].release_number;
    
      this.commentReleaseNumber = this.comments[index].release_number;
      
      this.attachmentUnderEdit = this.comments[index].downloadedAttachments;
      this.mainActionTitle = this.fbConstants.buttonTitles.update
    }
    

  }

  public clearAttachments() {
    this.commentPostErrorText = null;
    this.fileInput.nativeElement.value = "";
    this.selectedFiles = null;
    this.mainActionTitle = this.fbConstants.buttonTitles.submit;
    
  }

  private convertBase64ToBlobData(base64Data: string, contentType: string = 'image/png', sliceSize = 512) {
    const byteCharacters = atob(base64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }

  private getBuildNumberOfSelectedRelease(): string {
    let buildOfRelease = this.items.find(build => {
      return build.release_number == this.selectedReleaseForComment;
    })
    return String(buildOfRelease.build_id);
  }

  private isVerifiedAdmin(item: Apps): boolean {
    let adminEmail = item.version.bundle.admins.filter(admin => this.authService.email === admin.email);
    let orgAdminEmail = [];
    if (!!item.organization) {
      orgAdminEmail = item.organization.admins.filter(email => this.authService.email === email);
      if ((adminEmail.length > 0 && adminEmail[0].verified == true) || (orgAdminEmail.length > 0)) {
        return true;
      }
    }
    return false;
  }

  private resetToInitialState() {
    this.editingIndex = null;
    this.commentString = "";
    // this.selectedReleaseForComment = null;
    this.mainActionTitle = this.fbConstants.buttonTitles.submit;
    this.selectedFiles = null
  }

}
