import { Injectable, EventEmitter, Output, Directive } from '@angular/core';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { FbConstants } from 'src/app/utils/fb-constants';
import { Observable } from 'rxjs';
import { NetworkService } from 'src/app/commonServices/network.service';
import { environment } from '../../environments/environment';
declare var Razorpay: any;
declare var payMentCompleted: any;
@Directive()
@Injectable({   providedIn: 'root' })
export class PaymentService {
  options = {};
  rzp1: any;
  userToken;

  @Output() responseEmitter: EventEmitter<any> = new EventEmitter();
  constructor( private networkService: NetworkService,
               private fbApiConstants: FbApiConstants,
               private fbConstants: FbConstants
             ) {
    this.payMentCompleted('');
  }
  getAllPlansDetails(currency: string): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.paymentDetails +
    '?currency=' + currency);
  }
  initializePaymentService(plan, currency: string) {
    if (localStorage.getItem(this.fbConstants.localStorageItems.userInfo) !== null) {
      this.userToken = JSON.parse(localStorage.getItem(
                       this.fbConstants.localStorageItems.userInfo)).data.json_token.access;
    } else {
      this.userToken = null;
    }
    let loggedInUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
    this.options = {
      "prefill": {
        "name": loggedInUser.data.firstName,
        "email": loggedInUser.data.email,
      },
      'currency' : currency,
      'key': environment.paymentDetails.key,
      'amount': plan.price, // 2000 paise = INR 20
      'name': 'Flybuilds',
      'description': plan.heroTitle,
      'image': 'assets/images/razorpay-logo-final.png',
      'callback_url' : environment.urls.baseUrl +
                       '/api/auth/transactiondetails/?userid=' +
                       this.userToken + '&currency=' + currency,
      'theme': {
          'color': this.fbConstants.colorCode.defaultHex
      },
      'modal': {
          'ondismiss': this.payMentViewClosedByUser
      }
    };
    this.rzp1 = new Razorpay(this.options, this.payMentCompleted);
    this.rzp1.open();
  }
  payMentCompleted(response) {
  }
  payMentViewClosedByUser() {
    //alert('Payment Failed');
  }
}
