import Swal, { SweetAlertIcon } from "sweetalert2";

export class AlertUtil {

    /**
     * 
     * @param title 
     * @param message 
     * @param alertType 
     * 
     * Usage example: 
     * AlertUtil.showAlert('Oops!',err ||
        this.fbConstants.dialog_messages.commonServerError, this.fbConstants.dialog_type.error as SweetAlertIcon)
     */
    static showAlert(title: string, message: string, alertType: SweetAlertIcon) { 
        const loggedInUser = JSON.parse(localStorage.getItem('user_info'));
        if (loggedInUser != null) {
            Swal.fire(title, message, alertType);
        }
     }
}
