import { Injectable } from '@angular/core';
import { AppsService } from '../../../../../src/app/commonServices/apps.service';
import { FbConstants } from '../../../../../src/app/utils/fb-constants';
import { take,map } from 'rxjs/operators';
import { LoginCheckService } from './login-check.service';
import Swal from 'sweetalert2';


@Injectable({
  providedIn: 'root'
})
export class BuildPermissionCheckService {

  constructor(private appsService: AppsService, private fbConstants: FbConstants,
              private loginCheckService: LoginCheckService) { }

  checkIfPrivate(build_id) { // checking whether the build is public or private
    return this.appsService.getDetails(build_id).pipe( take(1), map((result) => {
      if (result.build_data !== null) { // public build
        return false;
      } else {
        return true;
      }
    },
    (err) => {
      Swal.fire('Oops!', err || err.message ||
        this.fbConstants.dialog_messages.commonServerError, 'error');
    }));
  }
}
