import { Injectable } from '@angular/core';
import { NetworkService } from 'src/app/commonServices/network.service';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class UploadService {

  constructor( private networkService: NetworkService, private fbApiConstants: FbApiConstants ) { }
  uploadBuild(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.file_upload);
    }
  shareBuild(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.shareBuild);
    }
  shareDemoBuild(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.demo);
  }
  sendUserAuthCredetials(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.authtoken);
  }
  sendVerifyOwnerEmail(code, app_label): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.verifyAdmin + '?code=' + code +
      '&app_label=' + app_label);
  }
  sendVerifyOrganizationOwnerEmail(code,user, id): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.verifyAdmin + '?code=' + code + '&user=' + user +
      '&id=' + id);
  }
  appendBuildWithLabel(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.assignAppLabel);
  }
}
