import { Component, OnInit, OnDestroy, NgZone } from '@angular/core';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { FbConstants } from 'src/app/utils/fb-constants';
import Swal from 'sweetalert2';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { environment } from '../../../environments/environment';
import { LoginService } from 'src/app/commonServices/login.service';
import { LoaderService } from '../../../../../../src/app/commonServices/loader.service';


enum UserManagementActions {
  resetPassword = 'resetPassword',
  recoverEmail = 'recoverEmail',
  verifyEmail = 'verifyEmail'
}

@Component({
  selector: 'app-user-auth',
  templateUrl: './user-auth.component.html',
  styleUrls: ['./user-auth.component.css',
              '../../../assets/css/bootstrap.min.css'
             ]
})

export class UserAuthComponent implements OnInit, OnDestroy {
  ngUnsubscribe: Subject<any> = new Subject<any>();
  actions = UserManagementActions;
  mode: string; // The user management action to be completed.
  actionCode: string;  // Just a code Firebase uses to prove that this is a real password reset.

  newPassword: string;
  confirmPassword: string;
  siteKey;
  captchaResponse;
  actionCodeChecked: boolean;
  isEmailVerified: boolean;
  isSubmitAttempt: boolean;
  baseUrl = environment.urls.domain_url;

  resetPasswordForm = new UntypedFormGroup({
    newPassword: new UntypedFormControl('', [Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.string_without_space),
      Validators.minLength(6)
    ])]),
    confirmPassword: new UntypedFormControl('', [Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.string_without_space),
      Validators.minLength(6)
    ])]),
    captcha: new UntypedFormControl('', Validators.required)
  });

  constructor( private router: Router,
               private activatedRoute: ActivatedRoute,
               private afAuth: AngularFireAuth,
               private authService: FirebaseAuthService,
               private fbConstants: FbConstants,
               private zone: NgZone,
               private loginService: LoginService,
               public loaderService :LoaderService
              ) { }

  ngOnInit() {
    this.siteKey = environment.recaptchaDetails.site_key;
    this.loaderService.showLoader = true;
    this.activatedRoute.queryParams.pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(params => {
      // if we didn't receive any parameters,
      // we can't do anything
      if (!params) {
        this.router.navigate(['/home']);
      }
      this.mode = params[this.fbConstants.stringLiterals.mode];
      this.actionCode = params[this.fbConstants.stringLiterals.oobCode];

      switch (params[this.fbConstants.stringLiterals.mode]) {
        case UserManagementActions.resetPassword: {
          // Check current authstate to decide
          // whether to change along with the current password or not
          // Verify the password reset code is valid.
          this.authService.getAuth().verifyPasswordResetCode(this.actionCode)
            .then(email => {
                              this.actionCodeChecked = true;
                              this.loaderService.showLoader = false;
            }).catch(e => {
            // Invalid or expired action code. Ask user to try to
            // reset the password again.
            Swal.fire({
              title: 'Link has expired!',
              text: this.fbConstants.dialog_messages.reset_link_expired,
              icon: 'error'
            });
            this.loaderService.showLoader = false;
            this.router.navigate([this.fbConstants.actions.login]);
          });
          break;
        }
        case UserManagementActions.recoverEmail: {
          break;
        }
        case UserManagementActions.verifyEmail: {
          // Verify the email verification code is valid.
          this.authService.getAuth().checkActionCode(this.actionCode)
            .then(email => {
              this.actionCodeChecked = true;
              this.loaderService.showLoader = false;
              this.handleVerifyEmail();
            }).catch(e => {
            // Invalid or expired action code. Ask user to try to
            // reset the password again.
            Swal.fire({
              title: 'Verification failed!',
              text: this.fbConstants.dialog_messages.verification_link_expired,
              icon: 'error',
            });
            this.loaderService.showLoader = false;
            this.router.navigate([this.fbConstants.actions.login]);
          });
          break;
        }
        default: {
          this.loaderService.showLoader = true;
          this.router.navigate([this.fbConstants.web_router_links.signin]);
        }
      }
    });
  }

  ngOnDestroy() {
    // End all subscriptions listening to ngUnsubscribe
    // to avoid memory leaks.
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  resolved(captchaResponse: string) { // captcha
    this.captchaResponse = captchaResponse;
  }

  /**
   * Attempt to confirm the password reset with firebase and
   * navigate user back to home.
   */
  handleResetPassword() {
    this.isSubmitAttempt = true;
    if (this.resetPasswordForm.valid) {
      if (this.resetPasswordForm.value.newPassword.trim()!==
        this.resetPasswordForm.value.confirmPassword.trim()) {
        Swal.fire('Password mismatch', this.fbConstants.dialog_messages.password_mismatch, 'warning')
          .then( res => {
            this.isSubmitAttempt = false;
          });
        this.resetPasswordForm.reset();
        return;
      }
      this.loginService.sendPasswordCaptcha(this.captchaResponse).subscribe(response => {
        if (response) {
          this.authService.getAuth().confirmPasswordReset(  // Save the new password.
            this.actionCode,
            this.resetPasswordForm.value.newPassword.trim())
            .then(resp => { // Password reset has been confirmed and new password updated.
              Swal.fire({title: 'Your password has been changed successfully',
                text: null,
                icon: 'success',
                confirmButtonText: 'LOGIN NOW'}).then(res => {
                  this.router.navigate([this.fbConstants.actions.login]);
                });
              }).catch(e => {
              // Error occurred during confirmation. The code might have
              // expired or the password is too weak.
              this.isSubmitAttempt = false;
              Swal.fire('Password reset failed!', e.message, 'error');
            });
           } // if response
          },
          (err) => {
            this.zone.run(() => {
              Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError,
              'error');
            });
          }
          );
    } // if valid
  }

  handleVerifyEmail() {
    // Try to apply the email verification code.
    this.authService.getAuth().applyActionCode(this.actionCode).then(resp => {
      this.isEmailVerified = true;
      // Email address has been verified.
    }).catch(e => {
      // Code is invalid or expired. Ask the user to verify their email address
      // again.
      Swal.fire('Email verification failed!', e.message, 'error');
    });
  }
  getErrorMessage(control) {
    return control.hasError('required')
           && this.isSubmitAttempt ? 'This field is required' :
           control.hasError('pattern') ? 'Whitespaces are not allowed' :
           control.hasError('minlength') ? 'Password must contain at least 6 characters' :
           '';
  }
}
