import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { FbConstants } from '../../../../../src/app/utils/fb-constants';
import { Router, ActivatedRoute } from '@angular/router';
import { take,map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class LoginCheckService {

isLoggedIn = false;

  constructor(private afAuth: AngularFireAuth, private fbConstants: FbConstants, private router: Router,
              private activatedRoute: ActivatedRoute) { }

    isUserLoggedIn() {
      return this.afAuth.authState.pipe( take(1), map((auth) => {
        if ( auth &&
            JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)) !== null) {
            return true;
        } else {
            return false;
        }
      }));
  }
}
