import { Component, OnInit, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { AppsService } from 'src/app/commonServices/apps.service';
import { Apps } from 'src/app/models/apps';
import { ActivatedRoute, Router } from '@angular/router';
import { FbConstants } from 'src/app/utils/fb-constants';
import Swal from 'sweetalert2';
import { Team } from 'src/app/models/team';
import { DeviceDetectorService } from 'ngx-device-detector';
import { DomSanitizer } from '@angular/platform-browser';
import { FbApiConstants } from 'src/app/utils/fb-api-constants';
import { Location } from '@angular/common';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { LoginService } from 'src/app/commonServices/login.service';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { LoginCheckService } from '../../mobileAppServices/login-check.service';
import { environment } from '../../../environments/environment';
import { AuthGuardService } from '../../mobileAppServices/auth-guard.service';
import { BuildPermissionCheckService } from '../../mobileAppServices/build-permission-check.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AppDetailsResponse } from 'src/app/models/appdetails-response';
import { AppComment } from 'src/app/models/app-comment';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'app-appdetails',
  templateUrl: './appdetails.component.html',
  styleUrls: ['./appdetails.component.css']
})
export class AppdetailsComponent implements OnInit, AfterViewChecked {
  appsList: Apps[] = [];
  appComments: AppComment[];
  appInfo: Apps;
  show = false;
  os_version;
  hidden;
  isDeleted;
  downloadUrl;
  permission_flag: string;
  teamArray: Team[] = [];
  emailList: string[] = [];
  usersList = [];
  platform;
  btnEnabled;
  isLoggedIn: boolean;
  device_OS;
  buildsList: Apps[] = [];
  isSupportingOS: boolean;
  visible = false;
  showBuilds =  false;
  isThroughLink: boolean;
  maxLength = 200;
  isButtonClicked: boolean;
  isOverFlowing: boolean;
  paragraphs = [
    {
      id: 1,
      content: '',
      visible: false
    },
  ];
  public hasOrganization: boolean = false;

  downloadInProgress: boolean = false;

  constructor( private appsService: AppsService, private route: ActivatedRoute,
               public fbConstants: FbConstants, private deviceService: DeviceDetectorService,
               public fbApiConstants: FbApiConstants, private cdRef: ChangeDetectorRef,
               public loaderService: LoaderService, private authService: FirebaseAuthService,
               private router: Router, private afAuth: AngularFireAuth,
               private loginCheckService: LoginCheckService, private authGuardService: AuthGuardService,
               private buildPermissionService: BuildPermissionCheckService, private snackBar: MatSnackBar ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      const label =params ['label'];
      const platform = params['platform'];
      const build_id = params['build_id'];
      if (label!== undefined && platform !== undefined) {
        this.loadAppDetails(label, platform);
        this.isThroughLink = false;
      } else if (build_id !== undefined) {
        this.getAppDetails(build_id);
        this.isThroughLink = true;
      }
    });
  }
  ngAfterViewChecked() {
    const show = this.checkOverflow();
    if (show !== this.isOverFlowing) { // check if it change, tell CD update view
      this.isOverFlowing = show;
      this.cdRef.detectChanges();
    }
  }
  private loadAppDetails(label, platform) {
    this.loaderService.showLoader = true;
    this.appsService.getAppDetails(label, platform).subscribe(
      (result: AppDetailsResponse) =>  {
        this.loaderService.showLoader = false;
      this.appsList = result.builds;
      this.appComments = result.comments;
      this.appInfo = this.appsList[0];
      this.permission_flag = this.appInfo.permission_flag;
    
      this.paragraphs[0].content = this.appInfo.description;
      this.paragraphs[0].visible = false;
      this.buildsList = this.appsList.filter(appObj => appObj !== this.appInfo);
      this.hasOrganization = this.appInfo.organization != null;
    },
    (err) => {
        Swal.fire('Oops!', err || this.fbConstants.dialog_messages.commonServerError, 'error');
     }
    );
  }
  private getAppDetails (build_id) {

      this.buildPermissionService.checkIfPrivate(build_id).subscribe( result => {
        if (result) {
          this.loginCheckService.isUserLoggedIn().subscribe( res => {
            if (res) {
              this.isLoggedIn = true;
              this.getPrivateBuild(build_id);
            } else {
              this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component],
                    {queryParams: {build_id: build_id}});
            }
          });
        } else {
          this.loginCheckService.isUserLoggedIn().subscribe( res => {
            if (res) {
              this.isLoggedIn = true;
            }
            this.getPublicBuild(build_id);
          });
        }
      });
  }
  private getPrivateBuild(build_id) {
    this.loaderService.showLoader = true;
    
    this.appsService.getPrivateBuildDetails(build_id).subscribe( result => {

      this.loaderService.showLoader = false;
      this.appsList.push(result.build_data);
      this.appComments = result.comments;
      this.appInfo = result.build_data;
      this.permission_flag = this.appInfo.permission_flag;
      this.paragraphs[0].content = this.appInfo.description;
      this.paragraphs[0].visible = false;
      this.isOverFlowing = this.checkOverflow();
      this.hasOrganization = this.appInfo.organization != null;
      },
      (err) => {
        this.loaderService.showLoader = false;
        Swal.fire('Oops!', err || err.message ||
              this.fbConstants.dialog_messages.commonServerError, 'error');
              this.onClickBack();
    });
  
  }
  private getPublicBuild(build_id) {
    this.loaderService.showLoader = true;
    this.appsService.getDetails(build_id).subscribe( result => {
      this.loaderService.showLoader = false;
      this.appInfo = result.build_data;
      this.permission_flag = this.appInfo.permission_flag;
      this.paragraphs[0].content = this.appInfo.description;
      this.paragraphs[0].visible = false;
      this.isOverFlowing = this.checkOverflow();
      this.hasOrganization = this.appInfo.organization != null;
        },
        (err) => {
          Swal.fire('Oops!', err || err.message ||
                this.fbConstants.dialog_messages.commonServerError, 'error');
      });
    }  
    
showButton(app: Apps) {
  if(app.storage === null){
    app.infoString = this.fbConstants.tooltip.accessRevockedByAdmin;
    return true;
  }
  if (app.storage.is_authenticated === null) {
    app.infoString = this.fbConstants.dialog_messages.inactive_build;
    return true;
  } else if (app.delete === null) {
    app.infoString = this.fbConstants.dialog_messages.inactive_build;
    return true;
  } else if (app.hidden === true) {
    app.infoString = this.fbConstants.dialog_messages.inactive_build;
    return true;
  } else if (this.isOSSupported(app)) {
    return true;
  } else if (app.permission_flag === this.fbConstants.permissions.private) {
    if (this.isUserExists(app)) {
      return true;
    }
  } else {
    return false;
  }
}
isOSSupported(app: Apps) {
  let device_OS;
  const os_version = app.os_version;
    const deviceInfo = this.deviceService.getDeviceInfo();
    const isMobile = this.deviceService.isMobile();
    const isTablet = this.deviceService.isTablet();
      if (isMobile || isTablet) {
        if ( deviceInfo.os === 'iOS' || deviceInfo.os === 'Mac') {
          device_OS = 1;
        } else if (deviceInfo.os === 'Android') {
          device_OS = 2;
        }
        if (device_OS !== app.version.bundle.platform) {
         app.infoString = this.fbConstants.dialog_messages.unsupported_os;
         return true;
        } else {
          return false;
          // }
        }
    } else {
      app.infoString = this.fbConstants.dialog_messages.unsupported_os;
      return true;
    }
}
private isUserExists(appInfo) {
  this.hidden = appInfo.hidden;
  this.teamArray = appInfo.team_list;
  this.teamArray.forEach(element => {
    for (let i = 0 ; i < element.users.length; i++) {
      this.usersList.push(element.users[i]);
    }
  });
  this.emailList = this.removeDuplicateUsingFilter(this.usersList);
  const loggedInUser = JSON.parse(localStorage.
                       getItem(this.fbConstants.localStorageItems.userInfo));
  const isUserExists = (this.emailList.includes(this.authService.email ||
                       loggedInUser.data.email)) ||
                       (appInfo.uploader.email === this.authService.email);
    if (isUserExists ) {
        return false;
    } else {
      if (appInfo.uploader.email !== loggedInUser.data.email ) {
        appInfo.labelString = this.fbConstants.dialog_messages.download_access_denied;
        return true;
      }
    }
}
onClickDownloadBuild(app) {
  if (app.permission_flag !== this.fbConstants.permissions.public) {
    if (localStorage.getItem(this.fbConstants.localStorageItems.userInfo) !== null) {
      const token = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)).data.json_token.access;
      const url =this.fbApiConstants.API_URLS.private_build_url +
      '?id=' + app.build_id + '&token=' + token;
       this.checkURLExists(url, app.version.bundle.platform); 
    }
  } else {
      const url = this.fbApiConstants.API_URLS.public_build_url +
      '?id=' + app.build_id;
      this.checkURLExists(url, app.version.bundle.platform);
  }
}

private checkURLExists(url, osType: number) {
  
  this.appsService.downloadBuild(url).subscribe(
    (result) => {
      if (result) {
        const downloadURL = environment.urls.baseUrl + url + '&start_download=true';
        this.downloadInProgress = true
        // asynchronous with BLOB like strategy.
        this.downloadFileWrap(downloadURL, osType).subscribe(()=> {
          this.downloadInProgress = false;
        }, () => {
          this.downloadInProgress = false;
        })
        this.snackBar.open(this.fbConstants.dialog_messages.downloading, '', {
          duration: 5000,
          panelClass: this.fbConstants.snackbarType.info
        });
      } 
    },
    (err)=>{
      Swal.fire('Oops!', err || err.message ||
        this.fbConstants.dialog_messages.commonServerError, 'error');
  })
}
private removeDuplicateUsingFilter(arr) { // avoiding duplication of users using filter method
  const unique_array = arr.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  });
  return unique_array;
}
onAppChange(app: Apps) {
  this.appInfo = app;
  this.paragraphs[0].content = app.description;
  this.paragraphs[0].visible = false;
  this.isButtonClicked = false;
  this.isOverFlowing = this.checkOverflow();
  this.permission_flag = this.appInfo.permission_flag;
  this.buildsList = this.appsList.filter(appObj => appObj !== this.appInfo);
}
checkOverflow() {
   const el = document.getElementById('content');
   if (el) {
    const isOverflowing = (el.clientWidth < el.scrollWidth
      || el.clientHeight < el.scrollHeight) || this.isButtonClicked;
    return isOverflowing;
   }
}
onClickLogin() {
  this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component]);
}
  onClickBack() {
    this.router.navigate([this.fbConstants.mob_router_links.dashboard_component]);
  }


  downloadFileWrap(url: string, osType: number): Observable<Observable<boolean>> {
    if(osType === 1) {
      window.location.href = url
      this.downloadInProgress = false;
    return of(of(true))
    }
    return this.appsService.downloadFile(url)
  }

}
