import { Component, OnInit, NgZone, Input } from '@angular/core';
import { FbConstants } from 'src/app/utils/fb-constants';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { LoginService } from 'src/app/commonServices/login.service';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { UntypedFormGroup, UntypedFormControl, Validators, UntypedFormBuilder } from '@angular/forms';
import { Plans } from 'src/app/models/payment';
import { Contact } from 'src/app/models/contact';
import { DeviceDetectorService } from 'ngx-device-detector';
import { TryItNowComponent } from '../try-it-now/try-it-now.component';
import { SigninComponent } from '../signin/signin.component';
import Swal from 'sweetalert2';
import { trimValidator } from 'src/app/utils/trim.validator';
import { environment } from '../../../environments/environment';
import { LocationDetectorService } from 'src/app/commonServices/location-detector.service';
import { LowerCasePipe } from '@angular/common';
import { PaymentService } from 'projects/web-app/src/app/webAppServices/payment.service';

declare var jQuery: any;
const $ = jQuery;

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.css',
              '../../../assets/css/bootstrap.min.css',
              '../../../assets/fonts/line-icons.css',
              '../../../assets/css/slicknav.css',
              '../../../assets/css/animate.css',
              '../../../assets/css/main.css',
              '../../../assets/css/responsive.css'],
  //  encapsulation: ViewEncapsulation.None
})
export class LandingPageComponent implements OnInit {
  // @ViewChild('videoPlayer') videoplayer: ElementRef;
  @Input() videoSource = 'assets/videos/fly-final.mp4';
  @Input() accept = this.fbConstants.extensions.attachments;

  plans: Plans[];
  isPrivate;
  supportForm: UntypedFormGroup;
  isFormSubmitAttempt: boolean;
  isLandingPage: string;
  siteKey;
  captchaResponse: string;
  userCountry;
  standardUserPrice;
  currency;
  file: File;
  imageUrl;
  fileName;
  params: Contact;
  isFilesizeExceeds: boolean;
  loading: boolean;
  isCaptcha: boolean;

  constructor( public fbConstants: FbConstants,
               private dialog: MatDialog,
               private router: Router,
               public loaderService: LoaderService,
               private loginService: LoginService,
               private zone: NgZone,
               private deviceService: DeviceDetectorService,
               private snackBar: MatSnackBar,
               private formBuilder: UntypedFormBuilder,
               private route: ActivatedRoute,
               private locDetector: LocationDetectorService,
               private paymentServices: PaymentService,) { }
 
  ngOnInit() {
    this.detectDevice()
    this.siteKey = environment.recaptchaDetails.site_key;
    if (this.router.url === this.fbConstants.actions.login) {
      this.dialog.open(SigninComponent, {data:
        {login_type: this.fbConstants.login_types.other},
        width: '800px', panelClass: 'custom-dialog-container',
        autoFocus: false});
    }
    this.locDetector.detectLocation().subscribe(response => {
        const status = JSON.parse(JSON.stringify(response)).status;
        this.userCountry = JSON.parse(JSON.stringify(response)).country;
        if ((status === this.fbConstants.status.success) &&
          (this.userCountry.toLowerCase() === this.fbConstants.country.baseCountry)) {
          this.currency = this.fbConstants.country.INCurrency;
        } else {
          this.currency = this.fbConstants.country.USCurrency;
        }
      this.paymentServices.getAllPlansDetails(this.currency).subscribe(resp => {
        this.plans = resp;
        const newplans = this.plans.sort((a, b) => {
          // Turn the strings into dates, and then subtract them
          // to get a value that is either negative, positive, or zero.
          return a.id - b.id;
        });
        this.plans = newplans;
        this.plans = this.plans.filter(plan => plan.type !== 'Free');
        this.plans.forEach(element => {
          const featuresList = element.features.split('$');
          element.features = featuresList;
        });
        let plan = this.plans.filter(plan => plan.type == 'Standard')[0];
        this.standardUserPrice = Number(plan.price) / 100;
      }, (err) => {});
    }, (err) => {
    });
    // this.detectDevice();
    this.supportForm = new UntypedFormGroup({
      name: new UntypedFormControl('', [Validators.required,
                                  trimValidator]),
      phone: new UntypedFormControl('', [ Validators.compose([
                                   Validators.pattern(this.fbConstants.patterns.phone),
                                   Validators.maxLength(20)
                                 ])]),
      email: new UntypedFormControl('', [Validators.compose([
                                   Validators.required,
                                   Validators.pattern(this.fbConstants.patterns.email)
      ])]),
      company: new UntypedFormControl('', [trimValidator]),
      message: new UntypedFormControl('', [ Validators.required, trimValidator]),
      recaptchaReactive: new UntypedFormControl('', Validators.required)
    });
  }

  onClickSubmit() {
    this.loading = true;
    this.isCaptcha = true;
    this.isFormSubmitAttempt = true;
    if (this.supportForm.valid) {
      this.params = this.supportForm.value;
      this.params.imageUrl = this.file;
      this.params.captcha_response = this.captchaResponse;
      this.loginService.sendSupportMessage(this.params).subscribe(result => {
        if (result) {
          this.loading = false;
          this.isCaptcha = false;
          this.isFormSubmitAttempt = false;
          Swal.fire('Message sent', result.message , 'success').then(res => {
              this.supportForm.reset({
                'name': '',
                'phone': '',
                'email': '',
                'company': '',
                'message': '',
               }); // reset fun
              this.fileName = '';
          });
        }
      }, (err) => {
        this.loaderService.showLoader = false;
        this.isFormSubmitAttempt = false;
        this.zone.run(() => {
          Swal.fire('Oops!', err ||
                this.fbConstants.dialog_messages.commonServerError, 'error');
        });
        });
    }
  }
  private detectDevice() {
    const browser = this.deviceService.browser;
    const browser_version = this.deviceService.browser_version
    

    if (browser === 'MS-Edge' || browser === 'Unknown') {
      this.snackBar.open(this.fbConstants.dialog_messages.safari_support,null,{
        panelClass: this.fbConstants.snackbarType.info
      });
    }
    if(browser==="Safari"){
      if(parseFloat(browser_version)<13){
        this.snackBar.open(this.fbConstants.dialog_messages.safari_support,null,{
              panelClass: this.fbConstants.snackbarType.info
            });
      }
    }
    // const isMobile = this.deviceService.isMobile();
    // if (isMobile) {
    //   this.snackBar.open(this.fbConstants.dialog_messages.mobile_support,null,{
    //     panelClass: this.fbConstants.snackbarType.info
    //   });
    // }
  }
 
  goSmoothScroll(anchor, ev) {
    ev.preventDefault();
    const element = document.querySelector('#' + anchor);
    if (element)  { element.scrollIntoView({ behavior: 'smooth', block: 'start' }); }
  }
  resolved(captchaResponse: string) {
    this.captchaResponse = captchaResponse;
  }

  submit(captchaResponse: string): void {
    if (captchaResponse === '') {
      alert('You can not proceed!');
  } else {
      alert('Thank you');
  }
  }
  onClickLogo() {
    window.scrollTo(0, 0);
  }

  onFileSelect(event) {
    this.isFilesizeExceeds = false;
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (events: any) => { // called once readAsDataURL is completed
          this.imageUrl = events.target.result;
      };
      reader.readAsDataURL(event.target.files[0]); // read file as data url
    }
    const fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      this.file = fileList[0];
      this.fileName = this.file.name;
      const filesize = this.file.size / 1024 / 1024;
      if ( filesize > 25) {
        this.isFilesizeExceeds = true;
      }
    }
  }

  onClickPrivacyPolicy() {
    window.open( environment.urls.domain_url + '/privacy-policy', '_blank');
  }

  scrollTo(className: string):void {
    const elementList = document.querySelectorAll('.' + className);
    const element = elementList[0] as HTMLElement;
    element.scrollIntoView({ behavior: 'smooth' });
  }
}

