import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Apps } from '../models/apps';
import { NetworkService } from './network.service';
import { FbApiConstants } from '../utils/fb-api-constants';
import { FbConstants } from '../utils/fb-constants';
import { AppPinRequest } from 'projects/web-app/src/app/webAppServices/models/app-pin-request';
import { AppHideRequest } from 'projects/web-app/src/app/webAppServices/models/app-hide-request';
import { AppComment } from '../models/app-comment';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';

@Injectable({ providedIn: 'root' })
export class AppsService {

  constructor(  private networkService: NetworkService,
                private fbApiConstants: FbApiConstants,
                private fbConstants: FbConstants,
                private httpClient: HttpClient
             ) { }
  showSharedTeams: boolean;
  showMembersList: boolean;
  shouldReload = false;
  // getting details of latest build of particular app //
  getAllApps(): Observable<Apps[]> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.app_list);
  }
  // getting details of recent apps //
  getRecentApps(): Observable<Apps[]> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.app_list +
      '?recent=true');
  }
  // getting details by bundle Id
  getAppDetails(params, platform): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.versions + params +
      '&platform=' + platform);
  }

  // getting comments by bundle Id
  getAppComments(appId: number): Observable<AppComment[]> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.appComments+appId+"/");
  }

  //delete commnet
  deleteAppComment(commentId: number):Observable<any> {
    return this.networkService.deleteRequest(this.fbApiConstants.API_URLS.appComment+commentId+"/");
  }

  //delete commnet
  deleteCommentAttachment(attachmentId: number):Observable<any> {
    return this.networkService.deleteRequest(this.fbApiConstants.API_URLS.viewAttachment+attachmentId+"/");
  }

  //Submitting comments for app
  postComments(
    app: number, 
    build: string, 
    commentString: string, 
    selectedFiles: FileList,
    )
    : Observable<any> {
    const formData = new FormData();
    formData.append(this.fbConstants.comment_params.app, String(app));
    formData.append(this.fbConstants.comment_params.build, build);
    formData.append(this.fbConstants.comment_params.content, commentString);
    if (selectedFiles != null) {
      for (let index = 0; index < selectedFiles.length; index++) {
        const file = selectedFiles.item(index)
        formData.append(this.fbConstants.comment_params.file, file);
      }
    }    
    return this.networkService.postRequest(formData, this.fbApiConstants.API_URLS.appComment);
  }

  getOverlaySettings(): Observable<any>{
    return this.networkService.postRequest(
      null,this.fbApiConstants.API_URLS.doNotShow);
  }

  //Updating comment
  updateComment(
    app: number, 
    build: string, 
    commentString: string, 
    selectedFiles: FileList,
    commentId: number,
    )
    : Observable<any> {
    const formData = new FormData();
    formData.append(this.fbConstants.comment_params.app, String(app));
    formData.append(this.fbConstants.comment_params.build, build);
    formData.append(this.fbConstants.comment_params.content, commentString);
    if (selectedFiles != null) {
      for (let index = 0; index < selectedFiles.length; index++) {
        const file = selectedFiles.item(index)
        formData.append(this.fbConstants.comment_params.file, file);
      }
    }    
    return this.networkService.putRequest(formData, this.fbApiConstants.API_URLS.appComment + commentId +"/");
  }

  //Updating attachment to a comment
  updateAttachment(
    commentId: number,
    selectedFiles: FileList,
    )
    : Observable<any> {
    const formData = new FormData();
    if (selectedFiles != null) {
      for (let index = 0; index < selectedFiles.length; index++) {
        const file = selectedFiles.item(index)
        formData.append(this.fbConstants.comment_params.file, file);
      }
    }    
    return this.networkService.postRequest(formData, this.fbApiConstants.API_URLS.addAttachment + commentId + "/");
  }

  // getting details by build Id //
  getBuildDetails(params) {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.app_list +
      '?build_id=' + params);
  }
  getDetails(params) {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.public_build_details +
           params);
  }
  // if build is private
  getPrivateBuildDetails(params) {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.private_build_details
           + params);
  }
  deleteUser(email, buildId): Observable<any> {
    return this.networkService.deleteRequest(this.fbApiConstants.API_URLS.removeUser
           + buildId, email);
  }
  // to delete build by build id //
  deleteBuild(label, buildId, appDelete, organization): Observable<any> {
  
    if (appDelete === true) {
      return this.networkService.deleteRequestParams(organization, this.fbApiConstants.API_URLS.deletebundle + '?label=' + label);
    } else {
      return this.networkService.deleteRequestParams(organization,    this.fbApiConstants.API_URLS.deletebuild + '?build_id=' + buildId);
    }
  }
  // to get the download count by build id
  informDownload(buildId): Observable<any> {
    const params = new FormData();
    params.append(this.fbConstants.build_info_keys.build_id, buildId);
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.appDownload);
  }
  getPrivateBuildUrl(url): Observable<any> {
    return this.networkService.getRequest(url);
  }
  resetAppPinState(label, value, platform): Observable<any> {
    const request = new AppPinRequest()
    request.pin = value
    request.label = label
    request.platform = platform
    return this.networkService.postRequestJson(request, this.fbApiConstants.API_URLS.appPin)
  }
  resetAppHiddenState(label, buildId, value, platform, isAppHide, organization): Observable<any> {
   const request = new AppHideRequest()
    request.hidden = value
    request.label = label
    request.build_id = buildId
    request.platform = platform
    if(organization != null) {
      request.organization = organization
    }
    if (isAppHide === true) {
      return this.networkService.postRequestJson(request, this.fbApiConstants.API_URLS.appHide + '?app=true');
    } else {
      return this.networkService.postRequestJson(request, this.fbApiConstants.API_URLS.appHide + '?app=false');
    }
  }
  shareBuild(buildId): Observable<any> {
    const params = new FormData();
    params.append(this.fbConstants.build_info_keys.build_id, buildId);
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.shareBuild);
  }
  resetPermissions(label, buildId, permissionFlag, isAppPermission, organization): Observable<any> {
    const params = new FormData();
    params.append(this.fbConstants.build_info_keys.app_label, label);
    params.append(this.fbConstants.build_info_keys.build_id, buildId);
    params.append(this.fbConstants.build_info_keys.permission_flag, permissionFlag);
    if(organization != null) {
      params.append(this.fbConstants.share_data.organization, organization);
    }
    if (isAppPermission) {
      return this.networkService.postRequest(params,
        this.fbApiConstants.API_URLS.resetPermissions + '?app=true');
    } else {
      return this.networkService.postRequest(params,
        this.fbApiConstants.API_URLS.resetPermissions + '?app=false');
    }
  }
  removeTeamAccess(teamId, buildId, user) {
    const params = new FormData();
    params.append(this.fbConstants.share_data.team_id, teamId);
    params.append(this.fbConstants.build_info_keys.build_id, buildId);
    if(user != null){
      params.append(this.fbConstants.share_data.email, user);
    }
    return this.networkService.postRequest(params,
           this.fbApiConstants.API_URLS.removeAccess);
  }
  userShared(buildId, value) {
    const params = new FormData();
    params.append(this.fbConstants.build_info_keys.build_id, buildId);
    params.append('value', value);
    this.networkService.postRequest(params, this.fbApiConstants.API_URLS.build_upload_proceed);
  }
  setAdmins(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.setAdmins);
  }
  updateAppTags(tags, label): Observable<any> {
    const params = new FormData();
    for (let index = 0; index < tags.length; index++) {
      params.append(this.fbConstants.build_info_keys.tags, tags[index]);
    }
    params.append(this.fbConstants.build_info_keys.app_label, label);
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.updateAppsTags);
  }
  updateTags(params): Observable<any> {
    return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.updateTags);
  }

  //API to get the attachements using id
  getAttachment(id: number): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.viewAttachment+id+"/");
  }
  public downloadBuild(url : string): Observable<any> {
    return this.networkService.getRequest(url)
  }

  downloadFile(urlExternal: string): Observable<Observable<boolean>> {
    return this.httpClient.get(urlExternal, { responseType: 'blob' as 'json' })
        .pipe(map((data: Blob) => {
          const url = window.URL.createObjectURL(data);
          const a = document.createElement('a');
          a.href = url;
          a.download = 'downloadedFile-'+ new Date().getTime().toString();
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          window.URL.revokeObjectURL(url);
         return of(true);
        }));
  }
}
