import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { environment } from 'projects/mobile-app/src/environments/environment';
import { Apps } from 'src/app/models/apps';
import { Device } from 'src/app/models/device';
import { FbConstants } from 'src/app/utils/fb-constants';
import { DeviceRegistrationService } from '../../mobileAppServices/device-registration.service';

@Component({
  selector: 'app-add-device',
  templateUrl: './add-device.component.html',
  styleUrls: ['./add-device.component.css']
})
export class AddDeviceComponent implements OnInit {

  public userDevices: Device[] = [];
  public app: Apps;

  constructor(private fbConstants: FbConstants,
    private router: Router,
    private deviceRegistrationService: DeviceRegistrationService,
    private deviceService: DeviceDetectorService,
    private snackBar: MatSnackBar,
  ) { }

  ngOnInit() {
    this.app = this.deviceRegistrationService.appInProgress;
  }

  addDeviceButtonAction() {
    const browser = this.deviceService.browser;
    
    if(browser != 'Safari') {
      this.snackBar.open(this.fbConstants.dialog_messages.device_profile_browser_support,null,{
        duration: 12000,
        panelClass: this.fbConstants.snackbarType.info
      });
    } else {
      const link = document.createElement('a');
      link.setAttribute('href', environment.urls.udid_profile);
      document.body.appendChild(link);
      link.click();
      link.remove();
    }
  }

}
