import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, defer, Observable, of, throwError } from 'rxjs';
import { catchError, delay, filter, take, tap } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { CurrentUser } from '../models/current-user';
import { FbApiConstants } from '../utils/fb-api-constants';
import { FbConstants } from '../utils/fb-constants';
import { NetworkService } from './network.service';

export interface RefreshTokenResult {
  access: string;
}

export class RefreshTokenRequest {
  refresh: string;
}

@Injectable({   providedIn: 'root' })
export class AuthService {

  constructor(
    private networkService: NetworkService,
    private fbConstants: FbConstants,
    private fbApiConstants: FbApiConstants,
    private router: Router,
  ) { };

  private tokenSubject$ = new BehaviorSubject<string | null>(null);

  token$ = this.tokenSubject$.pipe(
    filter((token) => token !== null),
    take(1)
  );

  refreshToken$: Observable<any> = defer(() => {
    if (this.tokenSubject$.value === null) {
      return this.token$;
    }
    // Defer allows us to easily execute some action when the Observable
    // is subscribed. Here, we set the current token to `null` until the
    // refresh operation is complete. This ensures no requests will be
    // sent with a known bad token.
    this.tokenSubject$.next(null);

    return this.refreshToken().pipe(
      tap((res) => {
        this.tokenSubject$.next(res.accessToken);
      }),
      catchError((err) => {
        this.logout();
        throw err;
      })
    );
  });

  get token(): string | null {
    return this.tokenSubject$.value;
  }

  authenticate(): void {
    this.tokenSubject$.next('someToken');
  }

  refreshToken(): Observable<any> {  
    const request = new RefreshTokenRequest()
    let userInfoParsed: CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));    
    if (userInfoParsed != null && userInfoParsed.data.json_token != null) {
      request.refresh = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)).data.json_token.refresh;
      return this.networkService.postRequestJson(request, this.fbApiConstants.API_URLS.refreshToken)
    }
    let errMsg = this.fbConstants.dialog_messages.sessionExpiredError;
    return throwError(errMsg);
  }

  logout(): void { 
    localStorage.removeItem(this.fbConstants.localStorageItems.userInfo);
    localStorage.removeItem(this.fbConstants.localStorageItems.login_type);
    localStorage.removeItem(this.fbConstants.localStorageItems.plan_type);
    localStorage.clear()
    
    if(NetworkService.isMobile(navigator.userAgent)) {
     
      window.location.href=environment.urls.baseUrl 
      
    } else {
      this.router.navigate([this.fbConstants.web_router_links.signin]);
    }
  }

}
