import { Component, OnInit, Input } from '@angular/core';
import { SigninComponent } from '../../signin/signin.component';
import { FbConstants } from 'src/app/utils/fb-constants';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.css']
})
export class PricingComponent implements OnInit {
  @Input() standardUserPrice:string;
  @Input() currency;
  isDialogueDisplayed:boolean = false;

  constructor(public fbConstants: FbConstants,private dialog: MatDialog) { }
  ngOnInit() {
  }

  public loading(){
    return (this.standardUserPrice == "" || this.standardUserPrice == undefined)
  }
  onClickSignIn() {
    this.isDialogueDisplayed = true
    this.dialog.open(SigninComponent, {data:
      {login_type: this.fbConstants.login_types.other},
      width: '800px', panelClass: 'custom-dialog-container',
      autoFocus: false, disableClose: true}).afterClosed().subscribe(result => {
        this.isDialogueDisplayed = false;
      });
  }
}
