import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UploadService } from 'src/app/commonServices/upload.service';
import { FbConstants } from 'src/app/utils/fb-constants';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-verify-owner',
  templateUrl: './verify-owner.component.html',
  styleUrls: ['./verify-owner.component.css']
})
// call when admin request comes and verify the email
export class VerifyOwnerComponent implements OnInit {

  code: string;
  app_label: string;
  formData = new FormData();
  user: string;
  organizationid: string;

  constructor(private activatedRoute: ActivatedRoute,  private uploadService: UploadService,
              private router: Router, private fbConstants: FbConstants ) { }

              ngOnInit() {
                this.activatedRoute.queryParams.subscribe( params => {
                  this.code = params['code'];
                  this.app_label = params['app_label'];
                  this.user = params['user'];
                  this.organizationid = params['organization_id'];
            
                  if(this.app_label != undefined){
                    this.uploadService.sendVerifyOwnerEmail(this.code, this.app_label).subscribe( result => {
                      if (result) {
                        Swal.fire({
                          title: this.fbConstants.dialog_titles.verify_admin,
                          text: this.fbConstants.dialog_messages.verify_app_admin,
                          icon: 'success',
                        }).then( res => { // goto signin component
                          this.router.navigate([this.fbConstants.web_router_links.home_component]);
                        });
                      }
                    }, (err) => {
                      Swal.fire({
                        title: this.fbConstants.dialog_titles.oops,
                        text: err || this.fbConstants.dialog_messages.commonServerError,
                        icon: 'error',
                      }).then(res => { // goto signin component
                        this.router.navigate([this.fbConstants.web_router_links.home_component]);
                      });
                    });
                  }else{
                      this.uploadService.sendVerifyOrganizationOwnerEmail(this.code, this.user, this.organizationid).subscribe(result => {
                        if (result) {
                          Swal.fire({
                            title: this.fbConstants.dialog_titles.verify_org_admin,
                            text: this.fbConstants.dialog_messages.verify_org_admin,
                            icon: 'success',
                          }).then(res => { // goto signin component
                            this.router.navigate([this.fbConstants.web_router_links.home_component]);
                          });
                        }
                      }, (err) => {
                        Swal.fire({
                          title: this.fbConstants.dialog_titles.oops,
                          text: err || this.fbConstants.dialog_messages.commonServerError,
                          icon: 'error',
                        }).then(res => { // goto signin component
                          this.router.navigate([this.fbConstants.web_router_links.home_component]);
                        });
                      });
                    }
                  });
                }
}
