import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { FbConstants } from '../utils/fb-constants';
import { NetworkService } from './network.service';
import { LoaderService } from './loader.service';
import { CurrentUser } from '../models/current-user';
import { FbApiConstants } from '../utils/fb-api-constants';

@Injectable({   providedIn: 'root' })
export class LoginService {
  isNewUser: boolean;
  constructor( private fbApiConstants: FbApiConstants,
               private fbConstants: FbConstants,
               private networkService: NetworkService,
               private loaderService: LoaderService) { }

  loginOrRegisterUser(user, token, params): Observable<CurrentUser> {
      params.append(this.fbConstants.user_details.first_name, user.given_name ||
                    user.first_name);
      params.append(this.fbConstants.user_details.firebase_token, token);
      if(user.family_name != null || user.last_name != null){
        params.append(this.fbConstants.user_details.last_name, user.family_name ||
          user.last_name );
      }
      params.append(this.fbConstants.user_details.email, user.email);
      params.append(this.fbConstants.user_details.login_type, user.loginType);
      return this.networkService.postRequest(params, this.fbApiConstants.API_URLS.register);
  }
  userLogout(): Observable<any> {
    return this.networkService.getRequest(this.fbApiConstants.API_URLS.logout);
  }
  sendSupportMessage(params): Observable<any> { // landing page support
    const formData = new FormData();
    formData.append(this.fbConstants.support_params.name, params.name);
    formData.append(this.fbConstants.support_params.phone, params.phone);
    formData.append(this.fbConstants.support_params.email, params.email);
    formData.append(this.fbConstants.support_params.company, params.company);
    formData.append(this.fbConstants.support_params.message, params.message);
    formData.append(this.fbConstants.support_params.captcha_response,
                    params.captcha_response);
    if (params.imageUrl !== undefined) {
      formData.append(this.fbConstants.support_params.imageUrl, params.imageUrl,
                      params.imageUrl.name);
    }
    return this.networkService.postRequest(formData, this.fbApiConstants.API_URLS.support);
  }
  sendUserSupportMessage(params): Observable<any> { // toolbar support
    const formData = new FormData();
    formData.append(this.fbConstants.support_params.name, params.name);
    formData.append(this.fbConstants.support_params.phone, params.phone);
    formData.append(this.fbConstants.support_params.email, params.email);
    formData.append(this.fbConstants.support_params.company, params.company);
    formData.append(this.fbConstants.support_params.message, params.message);
    if (params.imageUrl !== undefined) {
      formData.append(this.fbConstants.support_params.imageUrl, params.imageUrl,
                      params.imageUrl.name);
    }
    return this.networkService.postRequest(formData,
           this.fbApiConstants.API_URLS.user_support);
  }
  sendPasswordCaptcha(captcha?: string): Observable<any> {
    const formData = new FormData();
    formData.append(this.fbConstants.support_params.captcha_response, captcha);
    return this.networkService.postRequest(formData,
           this.fbApiConstants.API_URLS.passwordReset_captcha );
  }
}
