import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { FirebaseAuthService } from '../../../../../src/app/commonServices/firebase-auth.service';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { FbConstants } from '../../../../../src/app/utils/fb-constants';
import { LoginCheckService } from './login-check.service';
import { BuildPermissionCheckService } from './build-permission-check.service';
import { take,map } from 'rxjs/operators';



@Injectable(
  { providedIn: 'root' }
)
export class AuthGuardService {

  ifPermissionChecked = false;

  constructor(private authService: FirebaseAuthService, private router: Router, private afAuth: AngularFireAuth,
              private buildPermissionService: BuildPermissionCheckService, private fbConstants: FbConstants,
              private loginCheckService: LoginCheckService) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const build_id = route.queryParams['build_id'];
      if ( build_id !== undefined ) {
        this.buildPermissionService.checkIfPrivate(build_id).subscribe(result => { // if private build
          this.ifPermissionChecked = true;
          if (result) {
            this.loginCheckService.isUserLoggedIn().subscribe(res => { // if user loggedin
              if (res) {
                this.router.navigate([this.fbConstants.mob_router_links.app_details_component],
                      {queryParams: {build_id: build_id}});
              } else {
                this.router.navigate([this.fbConstants.mob_router_links.download_page_login_component],
                      {queryParams: {build_id: build_id}});
              }
            });
          } else { // if public build
            sessionStorage.setItem(this.fbConstants.sessionStorageItems.isNavigatedFromLogin, JSON.stringify(true));
            this.router.navigate([this.fbConstants.mob_router_links.app_details_component],
              {queryParams: {build_id: build_id}} );
          }
        });
    } else {
      return this.afAuth.authState.pipe(take(1), map((auth) => {
        // setTimeout(auth, 2000);
        if(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)!==null){
          localStorage.setItem(this.fbConstants.localStorageItems.userInfo, localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
          localStorage.setItem(this.fbConstants.localStorageItems.plan_type,
                         localStorage.getItem(this.fbConstants.localStorageItems.plan_type));
                         localStorage.setItem(this.fbConstants.localStorageItems.login_type,
                         localStorage.getItem(this.fbConstants.localStorageItems.login_type));
      }
        if ( auth &&
            JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)) !== null) {
            return true;
        } else {
            this.router.navigate([this.fbConstants.mob_router_links.signin]);
            return false;
        }
      })
    );
    }
  }
}
