import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { EMPTY, Observable } from 'rxjs';
import { catchError, concatMap, filter, finalize, take } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { FbConstants } from '../utils/fb-constants';
import { BehaviorSubject } from 'rxjs';
import { AuthService, RefreshTokenResult } from './auth.service';
import { CurrentUser } from '../models/current-user';
import { FbApiConstants } from '../utils/fb-api-constants';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({ providedIn: 'root' })
export class InterceptorService {
  public now: Date = new Date();
  isRefreshingToken = false;
  tokenRefreshed$ = new BehaviorSubject<boolean>(false);
  constructor(
    private fbConstants: FbConstants,
    private fbApiConstants: FbApiConstants,
    private authService: AuthService,
    private snackBar: MatSnackBar
  ) { }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      catchError(err => {
        let errMsg = {};
        if (err.status === 401) {
          if (request.url.includes(this.fbApiConstants.API_URLS.refreshToken)){
            errMsg = this.fbConstants.dialog_messages.sessionExpiredError;
            this.snackBar.open('Session expired, please login again!', '', {
              duration: 5000,
              panelClass: this.fbConstants.snackbarType.info
            });
            this.authService.logout();
             return EMPTY
            //return throwError(errMsg);
          }
          return this.handle401Error(request, next);
        } else if (err.status === 500) {
          errMsg = this.fbConstants.dialog_messages.commonServerError;
        } else if (err.status === 511) {
          const user = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
          user.auth_list = user.auth_list.filter(item => item !== err.error.upload_type);
          localStorage.setItem(this.fbConstants.localStorageItems.userInfo, JSON.stringify(user));
          errMsg = err.error.upload_type + this.fbConstants.dialog_messages.accessRemovedError;
        } else if(err.status === 502){
          errMsg = this.fbConstants.dialog_messages.timeout;
        } 
        else if((err.status === 403) && (err.url.includes(this.fbConstants.dialog_messages.buildUpload) )){
          return throwError(err)
        }
      else {
          errMsg = err.error.message;
        }
        return throwError(errMsg);
      })
    );
  }

  //Refrence: https://github.com/alexzuza/angular-refresh-token
  private handle401Error(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
    if (this.isRefreshingToken) {
      return this.tokenRefreshed$.pipe(
        filter(Boolean),
        take(1),
        concatMap(() => next.handle(this.addToken(req)))
      );
    }

    this.isRefreshingToken = true;

    // Reset here so that the following requests wait until the token
    // comes back from the refreshToken call.
    this.tokenRefreshed$.next(false);

    return this.authService.refreshToken().pipe(
      concatMap((res: RefreshTokenResult) => {
        const user: CurrentUser = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo));
        user.data.json_token.access = res.access;
        localStorage.setItem(this.fbConstants.localStorageItems.userInfo, JSON.stringify(user));
        this.tokenRefreshed$.next(true);
        return next.handle(this.addToken(req));
      }),
      catchError((err) => {
        this.authService.logout();
        this.snackBar.open('Session expired, please login again!', '', {
          duration: 5000,
          panelClass: this.fbConstants.snackbarType.info
        });
        let errMsg = this.fbConstants.dialog_messages.sessionExpiredError;
         return throwError(errMsg);
        //throw err;
      }),
      finalize(() => {
        this.isRefreshingToken = false;
      })
    );
  }

  addToken(req: HttpRequest<any>): HttpRequest<any> {
    const token = JSON.parse(localStorage.getItem(this.fbConstants.localStorageItems.userInfo)).data.json_token.access;
    return token ? req.clone({ setHeaders: { Authorization: this.fbConstants.auth_params.token_prefix + token } }) : req;
  }

}
