import {
  
  Component, 
  OnInit, 
  NgZone, 
  Inject, 
  Input, 
  Output, 
  EventEmitter 

} from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/commonServices/login.service';
import { FirebaseAuthService } from 'src/app/commonServices/firebase-auth.service';
import { FbConstants } from 'src/app/utils/fb-constants';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LoaderService } from 'src/app/commonServices/loader.service';
import { NetworkService } from 'src/app/commonServices/network.service';
import { 
  
  UntypedFormGroup, 
  UntypedFormControl, 
  Validators 

} from '@angular/forms';
import { User } from 'src/app/models/user';
import Swal from 'sweetalert2';
import { trimValidator } from 'src/app/utils/trim.validator';
import {MatSnackBar} from '@angular/material/snack-bar';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-account-creation',
  templateUrl: './account-creation.component.html',
  styleUrls: ['./account-creation.component.css'],
  preserveWhitespaces: false
})
export class AccountCreationComponent implements OnInit {
    isFirstnameEmpty: boolean;
    isLastnameEmpty: boolean;
    isEmailEmpty: boolean;
    isPasswordEmpty: boolean;
    isEmptyField: boolean;
    @Input() public emailToPopulate: string;
    @Output() public emailValueChange = new EventEmitter();

  params = new FormData();
  constructor( private router: Router,
               public loginService: LoginService,
               private authService: FirebaseAuthService,
               public fbConstants: FbConstants,
               public dialog: MatDialog,
               private loaderService: LoaderService,
               private zone: NgZone,
               private dialogRef: MatDialogRef<AccountCreationComponent>,
               @Inject(MAT_DIALOG_DATA) public data: any,
               private networkService: NetworkService,
               private snackBar: MatSnackBar
              ) {}

    signupForm = new UntypedFormGroup({
    email: new UntypedFormControl('', [Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.email)
    ])]),
    password : new UntypedFormControl('', [Validators.compose([
      Validators.required,
      Validators.pattern(this.fbConstants.patterns.string_without_space),
      Validators.minLength(6)
    ])]),
    firstName: new UntypedFormControl('', [Validators.required, trimValidator]),
    // lastName: new FormControl('', [Validators.required, trimValidator])
  });
  ngOnInit() {
    if(this.emailToPopulate != null){
      this.signupForm.patchValue({
        email: this.emailToPopulate, 
      });
    }
  }

  onClickSignUp(username: string, password: string) {
    this.isFirstnameEmpty = false;
    this.isLastnameEmpty = false;
    this.isEmailEmpty = false;
    this.isPasswordEmpty = false;
    if (this.signupForm.value.firstName === '' || this.signupForm.value.lastName === '' ||
         username === '' || password === '') {
           this.isEmptyField = true;
    } else if (this.signupForm.value.firstName === '') {
      this.isFirstnameEmpty = true;
    } else if ( this.signupForm.value.lastName === '' ) {
      this.isLastnameEmpty = true;
    } else if ( username === '' ) {
      this.isEmailEmpty = true;
    } else if ( password === '') {
      this.isPasswordEmpty = true;
    } else {
       if (this.signupForm.valid) {
        this.loaderService.showLoader = true;
        this.authService.signUpWithEmail(username.trim(), password.trim()).then((result) => {
          this.authService.sendEmail().then((response) => {
            const user: User = {} as User;
            user.email = this.signupForm.value.email.trim();
            user.given_name = this.signupForm.value.firstName.trim();
            // user.family_name = this.signupForm.value.lastName;
            user.loginType = this.fbConstants.login_types.other;
            this.loginService.loginOrRegisterUser(user, JSON.parse(JSON.stringify(result))
              .user.stsTokenManager.accessToken, this.params).subscribe( res => {
                if (res.data.json_token.access) {
                  this.loaderService.showLoader = false;
                  this.router.navigate([this.fbConstants.web_router_links.signin]);
                }
            },
            (err) => {
              this.loaderService.showLoader = false;
              this.zone.run(() => {
                Swal.fire('Oops!', err.message ||
                          this.fbConstants.dialog_messages.commonServerError, 'error');
              });
            });
            Swal.fire({
                        title: 'Verify your email',
                        html:  this.fbConstants.dialog_messages.email_verification  +
                        this.fbConstants.dialog_messages.email_note ,
                        icon: 'success',
                        confirmButtonText: 'OK',
                      }).then( res => {
                        this.onClickLogin();
                      });
            }).catch(err => {
              this.loaderService.showLoader = false;
              this.router.navigate([this.fbConstants.web_router_links.signin]);
            });
        },
        (err) => {
          this.loaderService.showLoader = false;
          if (err.code === this.fbConstants.firebaseErrorCodes.invalidEmail) {
            this.zone.run(() => {
              // Swal.fire('Oops!',
              //           this.fbConstants.dialog_messages.invalidPassword, 'error');
              this.snackBar.open(this.fbConstants.dialog_messages.invalidPassword, '', {
                duration: 3000,
                panelClass: this.fbConstants.snackbarType.error
              });
              });
          } else {
            this.zone.run(() => {
              Swal.fire('Oops!', err.message ||
                        this.fbConstants.dialog_messages.commonServerError, 'error'); // invalid credentials
                        });
          }
        }).catch(error => {
          this.loaderService.showLoader = false;
          Swal.fire('Oops!', error.message , 'error');
        });
      } // if valid
    } // else
}

  onClickLogin() {
    this.loginService.isNewUser = false;
    this.emailValueChange.emit(this.signupForm.value.email.trim());
  }
  getErrorMessage() {
    return this.signupForm.controls[this.fbConstants.stringLiterals.password]
              .hasError('required') ? 'This field is required' :
           this.signupForm.controls[this.fbConstants.stringLiterals.password]
              .hasError('pattern') ?
              'Whitespaces are not allowed' :
        this.signupForm.controls[this.fbConstants.stringLiterals.password]
              .hasError('minlength') ?
              'Password must contain at least 6 characters' : '';
  }
  close() {
    this.dialogRef.close();
  }

  onClickPrivacyPolicy() {
    window.open( environment.urls.domain_url + '/privacy-policy', '_blank');
  }
}
